import React, { useEffect, useState, useContext } from 'react';
import {
    Loader,
    dateByOrgHelper,
    dateHelper,
    logger
} from 'fc-ui-shared-components';
import { GeneralTabContext } from './context';
import { EmploymentTabContext } from '../context';
import { getByExternalId } from '../../../api/list';
import DeprecatedWorkRelationshipsContainer from './components/DEPRECATED-work-relationships/container';
import WorkRelationshipsContainer from './components/work-relationships/container';
import SupervisorContainer from './components/supervisors/container';
import OtherDatesTenureRetirement from './components/other-dates-tenure-retirement';
import StatusHistory from './components/status-history';
import './index.styl';
import workRelationshipApi from '../../../api/workRelationshipApi';

const GeneralTabContent = () => {
    const {
        $injector, $stateParams, org, employee, permissions, featureFlags
    } = useContext(EmploymentTabContext);

    const ssToastHlp = $injector.get('ssToastHlp');
    const [isLoading, setLoading] = useState(true);
    const [employmentGroups, setEmploymentGroups] = useState(null);
    const [workRelationships, setWorkRelationships] = useState([]);
    const [supervisors, setSupervisors] = useState([]);
    const [adding, setAdding] = useState(null);
    const [editing, setEditing] = useState(null);
    const [workRelationshipPermissions, setWorkRelationshipPermissions] = useState({
        resourceName: 'WorkRelationship',
        permittedActions: {
            View: false,
            Edit: false,
            Create: false,
            Delete: false
        },
        permittedPublishedProperties: {
            StaffType: 0
        },
        permittedActualProperties: {
            StaffType: 0
        }
    });

    const getStaffServiceWorkRelationshipsAndSetState = async () => {
        const getStaffServiceWorkRelationships = async () => workRelationshipApi.get($stateParams.employeeId);
        try {
            const staffMemberWRres = await getStaffServiceWorkRelationships();
            if (staffMemberWRres.data) {
                setWorkRelationships(staffMemberWRres.data);
            }
        } catch (ex) {
            logger.log(ex);
        }
    };

    useEffect(async () => {
        try {
            setSupervisors(employee._supervisors);

            if (featureFlags.fc_staff_service_work_relationship) {
                const workRelationshipPermissionsRes = await workRelationshipApi.getPermissions($stateParams.employeeId);
                if (workRelationshipPermissionsRes.data) {
                    setWorkRelationshipPermissions(workRelationshipPermissionsRes.data);
                    // don't attempt the call if we can't view workRelationships
                    if (workRelationshipPermissionsRes.data.permittedActions?.View) {
                        getStaffServiceWorkRelationshipsAndSetState();
                    }
                }
            } else {
                setWorkRelationships(employee._workRelationships);
            }

            const employmentGroupRes = await getByExternalId($injector, { listExternalId: 'jobtype.employmentgroups' });
            setEmploymentGroups(employmentGroupRes);
        } catch (e) {
            ssToastHlp.error(e);
        } finally {
            setLoading(false);
        }
    }, [employee]);

    const addSupervisor = () => {
        setAdding(true);
        const nextDay = (serverFromDate) => dateByOrgHelper.utcIsoToRelativeMoment(serverFromDate, org)
            .clone().add(1, 'days').startOf('day')
            .toISOString();
        let from = dateByOrgHelper.todaysStartRelativeTo(org, true);
        const lastestSupervisor = supervisors.sort((a, b) => new Date(b._from) - new Date(a._from))[0];
        if (lastestSupervisor) {
            from = dateHelper.isForeverFuture(lastestSupervisor._to)
                ? nextDay(lastestSupervisor._from)
                : nextDay(lastestSupervisor._to);
        }
        const supervisor = {
            id: null,
            _employeeId: '',
            _from: from,
            _to: dateHelper.foreverFuture
        };
        setSupervisors([supervisor, ...supervisors]);
    };

    const cancelNewSupervisor = () => {
        setSupervisors([...supervisors.filter((s) => s.id !== null)]);
        setAdding(false);
    };

    const context = {
        supervisors,
        workRelationships,
        employmentGroups,
        adding,
        editing,
        setAdding,
        setEditing,
        addSupervisor,
        cancelNewSupervisor,
        workRelationshipPermissions
    };

    const showEmploymentSection = permissions
        && (permissions.props.originalStartDate.view
            || permissions.props.hireDate.view
            || permissions.props.seniorityDate.view
            || permissions.props.tenureDate.view
            || permissions.props.retirementSystem.view
            || (featureFlags.isDataIntegrationsEnabled && permissions.props.stateEducatorId.view));

    return (
        <div className='general-tab-content'>
            <Loader loading={isLoading} fill={true} />
            {!isLoading && <React.Fragment>
                <GeneralTabContext.Provider value={context}>
                    {featureFlags.isGlobalFcSharedEmployeeEnabled
                        && <p className="shared-emp-disclaimer">The following details are for Frontline Central only.</p>
                    }
                    {featureFlags.fc_staff_service_work_relationship
                        ? <WorkRelationshipsContainer />
                        : <DeprecatedWorkRelationshipsContainer />}
                    {permissions.props.statusHistory.view && <StatusHistory />}
                    <SupervisorContainer />
                    {showEmploymentSection && <OtherDatesTenureRetirement />}
                </GeneralTabContext.Provider>
            </React.Fragment>}
        </div>
    );
};

GeneralTabContent.propTypes = {
};

export default GeneralTabContent;
