import React, { useContext } from 'react';
import { Typeahead, logger } from 'fc-ui-shared-components';
import { EmploymentTabContext } from '../../../../context';
import { GeneralTabContext } from '../../../context';
import { searchStaffSupervisor } from '../../../../../../api/employment';
import SupervisorOptionItem from './supervisorOptionItem';
import './styles.styl';

const SupervisorTypeahead = ({ ...props }) => {
    const { $injector, employee } = useContext(EmploymentTabContext);
    const { supervisors } = useContext(GeneralTabContext);

    const excludedEmployeeList = supervisors.filter((s) => s.isCurrent).map((s) => s._employeeId);
    excludedEmployeeList.push(employee.id);
    const handleSearch = (query) => searchStaffSupervisor($injector, query, excludedEmployeeList)
        .then((data) => data);

    const handleSelect = (value) => {
        logger.log(`selected supervisor id: ${value}`);
    };

    return (
        <Typeahead onSearch={handleSearch}
            onSelect={handleSelect}
            label="Name"
            optionsComponent={SupervisorOptionItem}
            {...props} />
    );
};

export default SupervisorTypeahead;
