import React, {
    useState, useContext, useEffect, useCallback
} from 'react';
import './index.styl';
import {
    SuperButton,
    Loader,
    formStatusEnum,
    dateByOrgHelper,
    iconHelper,
} from 'fc-ui-shared-components';
import moment from 'moment';
import classNames from 'classnames';
import { EmploymentTabContext } from '../context';
import { staffMemberHelper } from '../../../helpers';

const I9TabContent = () => {
    const [isLoading, setLoading] = useState(true);
    const [incompleteForms, setIncompleteForms] = useState([]);
    const [completedForm, setCompletedForm] = useState(null);
    const {
        $injector, employee, org
    } = useContext(EmploymentTabContext);
    const personnelLink = `/ec-employee/record/${employee.id}/personnel`;
    const frontlineI9Link = '/ec-forms/form-templates?frontlineI9';
    const formInstancesApi = $injector.get('ssFormInstancesApi');
    const toastHlp = $injector.get('ssToastHlp');
    const ssPermissionsSvc = $injector.get('ssPermissionsSvc');

    // force to array
    employee._i9forminstances = employee._i9forminstances instanceof Array ? employee._i9forminstances : [];

    async function processForms(forms) {
        try {
            const formRequests = forms.map((form) => formInstancesApi.get({
                id: form.id,
                include: 'steps',
                includeSensitive: false
            }, null, false));

            const formInstanceData = await Promise.all(formRequests);

            forms.forEach((form) => {
                form.canCompleteForm = false;
                form.sectionOneSubmittedDate = '';
                const matchingInstance = formInstanceData.find((f) => f.id === form.id);
                if (matchingInstance) {
                    form.canCompleteForm = matchingInstance._steps[1]._meta.permissions.permittedActions.edit;
                    form.sectionOneSubmittedDate = matchingInstance._steps[0]._submittedDtUtc;
                    form.sectionTwoSubmittedDate = matchingInstance._steps[1]._submittedDtUtc;
                }
            });
        } catch (e) {
            toastHlp.error(e);
        }
    }

    const handleTrackForm = useCallback(() => {
        const empName = staffMemberHelper.getFormattedFullName(employee);
        window.location.href = `/ec-forms/form-tracking/view-by-subject?search=${empName}`;
    }, [employee]);

    const formatSentDate = (date) => {
        const sentDateMoment = dateByOrgHelper.utcIsoToRelativeMoment(moment(date).toISOString(), org);
        return sentDateMoment.calendar(null, {
            sameDay: '[Today at] LT',
            lastDay: '[Yesterday at] LT',
            lastWeek: 'ddd [at] LT',
            sameElse: 'L [at] LT'
        });
    };

    // separate & order i9 forms
    function sortForms(forms) {
        const incForms = forms.filter((i) => i._status === formStatusEnum.incomplete);
        const compForms = forms.filter((i) => i._status === formStatusEnum.complete || i._status === formStatusEnum.completedAndDenied);
        // sort in descending order by form sent date
        [incForms, compForms].forEach((arr) => arr.sort((a, b) => (moment(a._formSentUtc) < moment(b._formSentUtc) ? 1 : -1)));
        return [incForms, (!compForms.length ? null : compForms[0])];
    }

    const formatPhone = (num) => {
        let city;
        let prefix;
        let number;
        let value = num;

        // clean it up.
        if (typeof num === 'number') { value = num.replace(/[^\d]/g, ''); }

        if (value.length === 11) { value = value.slice(1); }
        if (value.length === 10) {
            city = value.slice(0, 3);
            prefix = value.slice(3, 6);
            number = value.slice(6);
            return `(${city}) ${prefix}-${number}`;
        }
        return value;
    };

    useEffect(() => {
        (async () => {
            try {
                const [incForms, compForm] = sortForms(employee._i9forminstances);
                if (compForm) {
                    await processForms([compForm]);
                    setCompletedForm(compForm);
                } else if (incForms.length) {
                    await processForms(incForms);
                    setIncompleteForms(incForms);
                } else {
                    return;
                }
            } catch (e) {
                toastHlp.error(e);
            } finally {
                setLoading(false);
            }
        })();
    }, []);

    return (
        <div className="I9-tab-content">
            <header className="page-header">
                <h3 className="page-title">I-9 Employment Eligibility Verification</h3>
            </header>
            <Loader loading={isLoading} fill={true}/>
            {!isLoading && <div className="page-content">
                {!incompleteForms.length && !completedForm
                    ? <>
                        <div className="page-state-banner-group">
                            <p><strong>Note:</strong>&nbsp;This employee may have already completed their I-9. Check their <a href={personnelLink}>personnel documents.</a></p>
                        </div>
                        <div className="page-state-link-to-form-templates-group">
                            <p>Send <a href={frontlineI9Link}>Frontline I-9</a> from Form Templates, or as part of a packet.</p>
                        </div>
                    </>
                    : <section data-qa="form-data">
                        {!completedForm
                            ? <>
                                {incompleteForms.map((form, idx) => (
                                    <article className="form-instance" key={`${form.id}-${idx}`}>
                                        <p className="sent-date">sent {formatSentDate(form._formSentUtc)}</p>
                                        <section className="form-instance-section" data-qa={`${form.id}-section-1`}>
                                            <strong className='instruction'>Section 1 must be completed <em>no later than the first day</em> of employment, but not before accepting a job offer.</strong>
                                            <header className="form-instance-section-header">
                                                <div className="section-info-group step-info">
                                                    <h5>Section 1: Employee Information and Attestation</h5>
                                                    {form._currentStepIndex === 0
                                                        && <p><strong className="warn">Incomplete</strong> - to be completed by employee</p>}
                                                    {form._currentStepIndex === 1
                                                        && <p><i className={iconHelper.ssIconBefore('check-circle')}></i>Completed by employee {dateByOrgHelper.utcIsoToRelativeMoment(moment(form.sectionOneSubmittedDate).toISOString(), org).format('M/D/YY')}</p>}
                                                </div>
                                                <div className='section-info-group step-action'>
                                                    {ssPermissionsSvc.isPermittedAction('formInstances', 'track')
                                                        && <>
                                                            <SuperButton
                                                                label="Track Form"
                                                                type="button"
                                                                onClick={handleTrackForm}
                                                            ></SuperButton>
                                                        </>
                                                    }
                                                </div>
                                            </header>
                                        </section>
                                        <section className='form-instance-section' data-qa={`${form.id}-section-2`}>
                                            <strong className='instruction'>Section 2 must be completed and signed <em>within 3 business days</em> of the employee&rsquo;s first day of employment.</strong>
                                            <header className={classNames('form-instance-section-header', { 'is-disabled': form._currentStepIndex === 0 })}>
                                                <div className="section-info-group step-info">
                                                    <h5>Section 2: Employer or Authorized Representative Review and Verification</h5>
                                                    <p>To be completed by employer after section 1</p>
                                                </div>
                                                {form.canCompleteForm && <div className='section-info-group step-action'>
                                                    <a href={`/ec-forms/form-instance/${form.id}?returnUrl=/ec-forms/form-tracking&target=_blank`}>
                                                        <SuperButton
                                                            label="Complete Form"
                                                            type="button"
                                                        ></SuperButton>
                                                    </a>
                                                </div>}
                                            </header>
                                        </section>
                                    </article>
                                ))}
                            </>
                            : <>
                                <article className="form-instance" key={`${completedForm.id}`}>
                                    <section className="form-instance-section" data-qa={`${completedForm.id}-section-1`}>
                                        <header className="form-instance-section-header">
                                            <div className="section-info-group step-info">
                                                <h5>Section 1: Employee Information and Attestation</h5>
                                                <p><i className={iconHelper.ssIconBefore('check-circle')}></i>Completed by employee {dateByOrgHelper.utcIsoToRelativeMoment(moment(completedForm.sectionOneSubmittedDate).toISOString(), org).format('M/D/YY')}</p>
                                            </div>
                                        </header>
                                        <div className="form-instance-section-fields">
                                            <div className="field-row">
                                                <dl className="field-row-list">
                                                    <div>
                                                        <dt>Last Name</dt>
                                                        <dd>{employee._i9Data.lastName}</dd>
                                                    </div>
                                                    <div>
                                                        <dt>First Name</dt>
                                                        <dd>{employee._i9Data.firstName}</dd>
                                                    </div>
                                                    <div>
                                                        <dt>Middle Initial</dt>
                                                        <dd>{employee._i9Data.middleInitial}</dd>
                                                    </div>
                                                    <div>
                                                        <dt>Other Last Names (if any)</dt>
                                                        <dd>{employee._i9Data.otherLastName}</dd>
                                                    </div>
                                                </dl>
                                            </div>
                                            <div className="field-row">
                                                <dl className="field-row-list">
                                                    <div>
                                                        <dt>Address (street # and Name)</dt>
                                                        <dd>{employee._i9Data.address}</dd>
                                                    </div>
                                                    <div>
                                                        <dt>Apt. Number</dt>
                                                        <dd>{employee._i9Data.apt}</dd>
                                                    </div>
                                                    <div>
                                                        <dt>City or Town</dt>
                                                        <dd>{employee._i9Data.city}</dd>
                                                    </div>
                                                    <div>
                                                        <dt>state</dt>
                                                        <dd>{employee._i9Data.state}</dd>
                                                    </div>
                                                    <div>
                                                        <dt>zip code</dt>
                                                        <dd>{employee._i9Data.zip}</dd>
                                                    </div>
                                                </dl>
                                            </div>
                                            <div className="field-row">
                                                <dl className="field-row-list">
                                                    <div>
                                                        <dt>date of birth</dt>
                                                        <dd>{dateByOrgHelper.utcIsoToRelativeMoment(moment(employee._i9Data.dateOfBirth).toISOString(), org).format('MM/DD/YYYY')}</dd>
                                                    </div>
                                                    <div>
                                                        <dt>U.S. social security number</dt>
                                                        <dd>{employee._i9Data.ssn}</dd>
                                                    </div>
                                                    <div>
                                                        <dt>email address</dt>
                                                        <dd>{employee._i9Data.email}</dd>
                                                    </div>
                                                    <div>
                                                        <dt>phone number</dt>
                                                        <dd>{formatPhone(employee._i9Data.phone)}</dd>
                                                    </div>
                                                </dl>
                                            </div>
                                        </div>
                                    </section>
                                    <section className='form-instance-section' data-qa={`${completedForm.id}-section-2`}>
                                        <header className="form-instance-section-header">
                                            <div className="section-info-group step-info">
                                                <h5>Section 2: Employer or Authorized Representative Review and Verification</h5>
                                                <p><i className={iconHelper.ssIconBefore('check-circle')}></i>Completed by employer {dateByOrgHelper.utcIsoToRelativeMoment(moment(completedForm.sectionTwoSubmittedDate).toISOString(), org).format('M/D/YY')}</p>
                                            </div>
                                        </header>
                                    </section>
                                </article>
                            </>
                        }
                    </section>
                }
            </div>}
        </div>
    );
};

I9TabContent.propTypes = {
};

export default I9TabContent;
