import { SUPPLEMENTAL } from '../constants/actionTypes';

export const editField = (field, value) => ({
    type: SUPPLEMENTAL.EDIT_FIELD,
    payload: { field, value }
});

export const cancelSupplementalEdit = () => ({
    type: SUPPLEMENTAL.CANCEL
});

export const processing = (isProcessing = true) => ({
    type: SUPPLEMENTAL.PROCESSING,
    payload: isProcessing
});

export const showLoader = (show) => ({
    type: SUPPLEMENTAL.SHOWLOADER,
    payload: show
});

export const success = () => ({
    type: SUPPLEMENTAL.SUCCESS
});
