import React, { useContext, useState, useEffect } from 'react';
import PropTypes from 'prop-types';
import { useForm, FormProvider } from 'react-hook-form';
import classNames from 'classnames';
import moment from 'moment';
import {
    ArticleCard,
    DateRangeWithStatus,
    Header,
    MetaDataGrid,
    SelectFilterList,
    TextField,
    SuperActionMenu,
    SuperActionMenuItem,
    SuperButton,
    SuperSingleFilterList,
    SuperInput,
    Loader,
    dateHelper,
    dateByOrgHelper,
    validationHelper,
    logger
} from 'fc-ui-shared-components';
import { getActiveItems } from '../../../../api/list';
import { AssignmentsTabContext } from '../../context';
import AssignmentDetails from './assignment-details';
import { AssignmentContext } from './context';
import { useAssignmentForm } from '../../hooks/useAssignmentForm';

const Assignment = ({ position }) => {
    const isNew = position.id === null;
    const { _name, assignment } = position;
    const assignmentForm = useAssignmentForm(position, assignment, _name);
    const [expandDetails, setExpandDetails] = useState(false);
    const toggleExpandDetails = () => setExpandDetails(!expandDetails);
    const [jobType, setJobType] = useState({});
    const [tempExternalId, settempExternalId] = useState(null);
    const {
        state: tabState,
        positionTypes,
        jobTypes,
        locations,
        departments,
        contractGroups,
        employmentGroups,
        setEditMode,
        organization,
        getValidMinMaxAssignmentDates,
        $injector,
        isPermitted
    } = useContext(AssignmentsTabContext);

    const getJobTypeData = async (jobTypeId) => {
        if (jobTypeId === jobType.id) return;
        try {
            const jobTypeApi = $injector.get('ssJobTypesApi');
            const response = await jobTypeApi.get({
                id: jobTypeId,
                include: 'jobTypeDetails,paySchedules'
            });
            if (response) {
                response.employmentGroup = employmentGroups.find((g) => g.value === response._bargainingUnit.id) || { name: 'None' };
            }
            setJobType(response);
        } catch (err) {
            logger.error('There was an error getting JobTypeData', err);
        }
    };
    useEffect(() => {
        if (position._jobType && position._jobType.id) {
            getJobTypeData(position._jobType.id);
        }
        settempExternalId(assignmentForm.fields.externalId);
    }, []);

    const methods = useForm();

    useEffect(() => {
        if (tabState.editing === assignment.id || isNew) {
            setExpandDetails(true);
        }
    }, [tabState.editing]);

    const activePositions = getActiveItems(positionTypes, _name, 'value');
    const activeJobTypes = getActiveItems(jobTypes, position._jobType.id);
    const activeLocations = getActiveItems(locations, position._location.id);
    const activeDepartments = getActiveItems(departments, position._department.id);
    const selectedPosition = activePositions.find((p) => p.value === _name);
    const positionName = !selectedPosition ? 'None' : selectedPosition.name;
    let employmentGroupName = jobType?.employmentGroup?.name ?? 'None';
    if (isNew) {
        employmentGroupName = 'Determined by Job Type';
    }

    const triggerDetailDatesValidation = () => {
        const fieldNames = Object.keys(methods.getValues())
            .filter((k) => k.startsWith('detail-start') || k.startsWith('detail-end'));
        methods.trigger(fieldNames);
    };

    const getDetailsForDateValidation = () => {
        const order = [...assignmentForm.newDetails.map((n) => n.id), ...assignment._details.map((d) => d.id)];
        return assignmentForm.details
            .slice()
            .sort((a, b) => order.indexOf(a.id) - order.indexOf(b.id))
            .filter((c) => !c.deleted);
    };

    const validateDetailEndDateOverlap = (id, end) => {
        const details = getDetailsForDateValidation(); if (details.length <= 1) return true;
        const thisIndex = details.findIndex((d) => d.id === id);
        const hasDetailAfter = thisIndex !== 0;
        return (!hasDetailAfter
            || (dateHelper.isForeverPast(details[(thisIndex - 1)].fields.startDate)
            || moment(details[(thisIndex - 1)].fields.startDate) > moment(end)));
    };

    const validateDetailStartDateOverlap = (id, start) => {
        const details = getDetailsForDateValidation(); if (details.length <= 1) return true;
        const thisIndex = details.findIndex((d) => d.id === id);
        const hasDetailBefore = thisIndex !== details.length - 1;
        return (!hasDetailBefore
            || (dateHelper.isForeverPast(start) && dateHelper.isForeverFuture(details[(thisIndex + 1)].fields.endDate))
            || moment(details[(thisIndex + 1)].fields.endDate) < moment(start));
    };

    const context = {
        form: assignmentForm,
        isNew,
        isEditing: tabState.editing === assignment.id || isNew,
        editingDisabled: tabState.editing !== null || tabState.adding !== null,
        validateDetailEndDateOverlap,
        validateDetailStartDateOverlap,
        triggerDetailDatesValidation,
        permission: assignment.$permitted
    };

    const handleJobTypeChange = (id) => {
        assignmentForm.setJobTypeField(id);
        getJobTypeData(id);
    };

    const handleCancel = () => {
        methods.clearErrors();
        assignment._details = assignment._details.filter((x) => !x.isNew);
        getJobTypeData(position._jobType.id)
            .then(assignmentForm.cancel());
    };

    const isAssignmentDetailsEnabled = () => assignmentForm.fields.jobTypeId === '' || dateHelper.isForeverPast(assignmentForm.fields.startDate) || assignmentForm.isProcessing;

    const handleAddAssignmentDetails = () => {
        const newDetails = {
            isNew: true,
            id: `${(Math.ceil(Math.random() * 10000000)).toString()}`,
            _fromDateUtc: assignmentForm.fields.startDate,
            _toDateUtc: assignmentForm.fields.endDate,
            _payLane: '',
            _payStep: '',
            _payPeriodsPerYear: 0,
            _payRate: '',
            _payRateFrequency: 0,
            _hoursPerDay: 0,
            _workDaysPerYear: 0,
            _fTE: 0,
            _sickDaysPerSchoolYear: 0,
            _personalDaysPerSchoolYear: 0,
            _vacationDaysPerSchoolYear: 0
        };
        const sortDateDesc = (a, b) => (moment(a.fields.startDate) < moment(b.fields.startDate) ? 1 : -1);
        const assignmentDetails = assignmentForm.details.filter((d) => !d.deleted).sort(sortDateDesc);

        if (assignmentDetails.length) {
            const { endDate: lastEndDate } = assignmentDetails[0].fields;
            if (dateHelper.isForeverFuture(lastEndDate)) {
                newDetails._fromDateUtc = dateHelper.foreverPast;
                newDetails._toDateUtc = dateHelper.foreverFuture;
            } else if (moment(lastEndDate) < moment(assignmentForm.fields.endDate)) {
                newDetails._fromDateUtc = dateByOrgHelper.startOfDayRelativeTo(dateByOrgHelper.utcIsoToRelativeMoment(lastEndDate, organization).add(1, 'day'), organization);
            } else {
                newDetails._toDateUtc = dateHelper.foreverFuture;
            }
        }
        assignmentForm.addNewDetails(newDetails);
    };

    const updatePreviousDetails = (e, detailId) => {
        const details = getDetailsForDateValidation();
        if (details.length <= 1) return;
        const thisIndex = details.findIndex((d) => d.id === detailId);
        const hasDetailBefore = thisIndex !== details.length - 1;
        if (!hasDetailBefore) return;
        const previousDetail = details[(thisIndex + 1)];
        if (!dateHelper.isForeverFuture(previousDetail.fields.endDate)) return;
        const newEndDate = dateByOrgHelper.startOfDayRelativeTo(moment(e).subtract(1, 'day'), organization);
        assignmentForm.setDetailsField(previousDetail.id, 'endDate', newEndDate);
    };

    const filterDeletedDetails = (d) => !assignmentForm.details.find((f) => f.deleted && d.id === f.id);
    const getEndDate = (value) => (value == null ? dateHelper.foreverFuture : value);
    const validDates = getValidMinMaxAssignmentDates(assignment.id, isNew);
    const startDateRules = validationHelper.validateAssignmentDateRange(true, 'Start Date', validDates.start.min, validDates.start.max);
    let endDateRules = validationHelper.validateAssignmentDateRange(false, 'End Date', validDates.end.min, validDates.end.max);

    endDateRules = {
        ...endDateRules,
        validate: {
            isEndDateAfterStartDate: (endDate) => endDate && validationHelper.validateEndDateIsAfterStartDate(dateByOrgHelper.startOfDayRelativeTo(moment(assignmentForm.fields.startDate), organization), dateByOrgHelper.startOfDayRelativeTo(moment(endDate), organization)),
            ...endDateRules.validate
        }
    };

    const save = () => {
        assignmentForm.save(jobType);
        settempExternalId(assignmentForm.fields.externalId ? assignmentForm.fields.externalId : position.id);
    };

    return (
        <AssignmentContext.Provider value={context}>
            <FormProvider {...methods}>
                <form onSubmit={methods.handleSubmit(isNew ? () => assignmentForm.create(jobType) : () => save(jobType))} noValidate>
                    <ArticleCard
                        className={classNames('employee-record-card assignment-group', { 'new-assignment': isNew })}
                        data-test={`assignment-${isNew ? 'new' : position.id}`}>
                        <React.Fragment>
                            <Loader loading={assignmentForm.showLoader} fill={true} />
                            <Header title={isNew ? 'New Assignment' : positionName}>
                                <DateRangeWithStatus
                                    startDateName={`assignment-startdate-${assignment.id || 'new'}`}
                                    endDateName={`assignment-enddate-${assignment.id || 'new'}`}
                                    startDateMin={validDates.start.min}
                                    startDateMax={validDates.start.max}
                                    endDateMin={validDates.end.min}
                                    endDateMax={validDates.end.max}
                                    startDate={assignmentForm.fields.startDate}
                                    endDate={assignmentForm.fields.endDate}
                                    organization={organization}
                                    isEditing={context.isEditing}
                                    handleStartDateUpdate={(newValue) => assignmentForm.setField('startDate', newValue)}
                                    handleEndDateUpdate={(newValue) => assignmentForm.setField('endDate', getEndDate(newValue))}
                                    hideTag={context.isEditing || isNew}
                                    startDateRequired={true}
                                    endDateRequired={false}
                                    startDateRules={startDateRules}
                                    endDateRules={endDateRules}
                                    disabled={assignmentForm.isProcessing} />
                                <div className="actions">
                                    {!context.isEditing
                                        ? isPermitted('assignments', 'edit') && <SuperActionMenu
                                            label="Edit"
                                            ssIconAfter="angle-down"
                                            disabled={context.editingDisabled}>
                                            <SuperActionMenuItem
                                                label="Edit"
                                                onClick={() => setEditMode(assignment.id)} />
                                            {isPermitted('assignments', 'delete') && <SuperActionMenuItem
                                                label="Delete Assignment"
                                                onClick={assignmentForm.deleteAssignment}
                                                style={{ color: '#bf2e3a' }} /> }
                                        </SuperActionMenu>
                                        :										<span>
                                            {(!isNew && isPermitted('assignments', 'delete')) && <SuperButton
                                                label="Delete"
                                                className="super-button-container delete"
                                                type="button"
                                                styleType="flat"
                                                ssIconBefore="trash-o"
                                                onClick={assignmentForm.deleteAssignment}
                                                disabled={assignmentForm.isProcessing} />}
                                            <SuperButton
                                                label="Cancel"
                                                type="button"
                                                onClick={handleCancel}
                                                disabled={assignmentForm.isProcessing} />
                                            <SuperButton
                                                label="Save"
                                                type="submit"
                                                styleType="pri"
                                                ssIconBefore={assignmentForm.isProcessing ? 'spinner' : null}
                                                disabled={assignmentForm.isProcessing} />
                                        </span>
                                    }
                                </div>
                            </Header>
                            <MetaDataGrid.Container>
                                <MetaDataGrid.List>
                                    <MetaDataGrid.Item data-test="assignment-position">
                                        <SelectFilterList
                                            name="name"
                                            label="Position Name"
                                            items={activePositions}
                                            editMode={context.isEditing}
                                            databindName="name"
                                            value={assignmentForm.fields.name}
                                            onChange={(e) => assignmentForm.setField('name', e.target.value)}
                                            required={true}
                                            disabled={assignmentForm.isProcessing} />
                                    </MetaDataGrid.Item>
                                    <MetaDataGrid.Item data-test="assignment-job-type">
                                        <SelectFilterList
                                            label="Job Type"
                                            name="jobTypeId"
                                            items={activeJobTypes}
                                            editMode={context.isEditing}
                                            databindValue="id"
                                            databindName="_name"
                                            value={assignmentForm.fields.jobTypeId}
                                            onChange={(e) => handleJobTypeChange(e.target.value)}
                                            required={true}
                                            disabled={assignmentForm.isProcessing} />
                                    </MetaDataGrid.Item>
                                    <MetaDataGrid.Item data-test="assignment-contract-group">
                                        <SuperSingleFilterList
                                            label="Contract Group"
                                            items={contractGroups}
                                            editMode={context.isEditing}
                                            value={assignmentForm.fields.contractGroupId}
                                            onChange={(e) => assignmentForm.setField('contractGroupId', e.target.value)}
                                            disabled={assignmentForm.isProcessing} />
                                    </MetaDataGrid.Item>
                                    <MetaDataGrid.Item data-test="assignment-employment-group">
                                        <SuperInput
                                            label="Employment Group"
                                            editMode={false}
                                            value={employmentGroupName}
                                            disabled={true}/>
                                    </MetaDataGrid.Item>
                                    <MetaDataGrid.Item data-test="assignment-location">
                                        <SelectFilterList
                                            name="locationId"
                                            label="Location"
                                            items={activeLocations}
                                            editMode={context.isEditing}
                                            databindValue="id"
                                            value={assignmentForm.fields.locationId}
                                            onChange={(e) => assignmentForm.setField('locationId', e.target.value)}
                                            required={true}
                                            disabled={assignmentForm.isProcessing} />
                                    </MetaDataGrid.Item>
                                    <MetaDataGrid.Item data-test="assignment-department">
                                        <SelectFilterList
                                            name="departmentId"
                                            label="Department"
                                            items={activeDepartments}
                                            editMode={context.isEditing}
                                            databindValue="id"
                                            value={assignmentForm.fields.departmentId}
                                            onChange={(e) => assignmentForm.setField('departmentId', e.target.value)}
                                            required={true}
                                            disabled={assignmentForm.isProcessing} />
                                    </MetaDataGrid.Item>
                                    <MetaDataGrid.Item data-test="assignment-external-id">
                                        <TextField
                                            name="externalId"
                                            label="External ID"
                                            value={assignmentForm.fields.externalId}
                                            required={!!tempExternalId && tabState.editing === assignment.id}
                                            onChange={(e) => assignmentForm.setField('externalId', e.target.value)}
                                            editMode={context.isEditing}
                                            disabled={assignmentForm.isProcessing}
                                            customRules={tabState.editing === assignment.id ? validationHelper.validateNotOnlyWhitespace('External ID') : {}}
                                        />
                                    </MetaDataGrid.Item>
                                </MetaDataGrid.List>
                            </MetaDataGrid.Container>
                            {(assignment._details.length || context.isEditing) && (assignment.$permitted ? assignment.$permitted.props.details.view : true)
								&& <React.Fragment>
									 { (assignment._details.length || isPermitted('assignments', 'create'))
										&& <>
										    <hr className="assignment-details-divider" />

										    <h4 className="assignment-details-controls" data-test="assignment-detail-controls">
										        <SuperButton
										            label="Assignment Details"
										            styleType="flat"
										            onClick={toggleExpandDetails}
										            ssIconBefore={expandDetails ? 'caret-down' : 'caret-right'}
										            disabled={!assignment._details.length}
										        />
										        { (context.isEditing && isPermitted('assignments', 'create'))
													&& <SuperButton
													    ssIconBefore="plus-circle"
													    type="button"
													    styleType="smp"
													    label="Add New"
													    disabled={isAssignmentDetailsEnabled()}
													    onClick={handleAddAssignmentDetails}
													    className="container-add-button" />
										        }
										    </h4>
										</>
								    }
								    {expandDetails
                                    && <>
								        {assignmentForm.newDetails.map((d) => <AssignmentDetails key={d.id}
								            assignmentDetails={d}
								            validDates={validDates}
								            jobType={jobType}
								            updatePreviousDetails={updatePreviousDetails}
								            disabled={isAssignmentDetailsEnabled()} />)}
                                        {assignment._details.sort((a, b) => new Date(b._fromDateUtc) - new Date(a._fromDateUtc)).filter(filterDeletedDetails).map((d) => <AssignmentDetails key={d.id}
								            assignmentDetails={d}
								            validDates={validDates}
								            jobType={jobType}
								            updatePreviousDetails={updatePreviousDetails}
								            disabled={isAssignmentDetailsEnabled()} />)}
                                    </>}
								</React.Fragment>
                            }
                        </React.Fragment>
                    </ArticleCard>
                </form>
            </FormProvider>
        </AssignmentContext.Provider>
    );
};

Assignment.propTypes = {
    position: PropTypes.object.isRequired,
};

export default Assignment;
