import React, { useEffect, useState } from 'react';
import PropTypes from 'prop-types';
import _ from 'lodash';
import {
    SuperInputCheckbox,
    ToggleSwitch,
    Loader,
    SuperButton,
    Tooltip,
    angularHelper,
    medicalDocumentEnum,
    personnelDocumentEnum
} from 'fc-ui-shared-components';
import ExportProfileItem from './item';
import staffProfileInitialData from './profileData';
import './index.styl';

const ExportProfile = ({ $injector, $stateParams, $scope }) => {
    const staffProfileData = _.cloneDeep(staffProfileInitialData);
    const ssToastHlp = $injector.get('ssToastHlp');
    const [staffProfile, setStaffProfile] = useState(null);
    const [isLoading, setLoading] = useState(true);
    const [isExportButton, setExportStatus] = useState(false);
    const fileCountApi = $injector.get('empFilesApi');
    const ssEmployeeApi = $injector.get('ssEmployeeApi');
    const ssPermissionsSvc = $injector.get('ssPermissionsSvc');
    const routeVm = angularHelper.findInScope('routeVm', $scope);
    const [isButtonDisabled, setButtonDisabled] = useState({
        selectAll: false,
        deselectAll: false,
        isAnySelected: true
    });
    const categoryType = {
        personnel: 0,
        medical: 1
    };
    const isPermitted = (permissionGroup, property) => ssPermissionsSvc.isPermittedAction(permissionGroup, property);

    const isAllChecked = (data, value = true) => Object.entries(data).every((d) => d[1].isChecked === value);

    const hasPartialChecked = (data) => Object.entries(data).some((d) => d[1].isChecked) && !Object.entries(data).every((d) => d[1].isChecked);

    const getFileCount = async (categoryId) => {
        try {
            const response = await fileCountApi.getFileCounts($stateParams.employeeId, categoryId);
            if (response && response.data) {
                return response.data;
            }
        } catch (err) {
            ssToastHlp.error(err);
        }
        return null;
    };
    const getEmployeePermissions = async () => {
        const employeeResponse = await ssEmployeeApi.get({ id: $stateParams.employeeId });
        return employeeResponse ? employeeResponse.$permitted : null;
    };
    const mapDocumentsCount = (recordCount, recordType) => {
        const recordEnumType = recordType === categoryType.personnel ? personnelDocumentEnum : medicalDocumentEnum;
        const parentKey = recordType === categoryType.personnel ? 'personnelDocumentSummary' : 'medicalDocumentSummary';
        Object.keys(recordCount.items).forEach((property) => {
            const objectKey = recordEnumType.enumByValue(property).name;
            if (objectKey != null) {
                staffProfileData[parentKey].childData[0][objectKey].count = recordCount.items[property];
            }
        });
    };
    const mapPermissions = (permissions) => {
        Object.entries(staffProfileData).forEach((profile) => {
            const tempParent = profile[1];
            if (tempParent.childData.length !== 0) {
                Object.entries(tempParent.childData[0]).forEach((child) => {
                    const tempChild = child[1];
                    if (tempChild.permissions.every((p) => permissions.props[p])) {
                        tempChild.hasPermission = tempChild.permissions.every((p) => permissions.props[p].view);
                    } else {
                        tempChild.hasPermission = isPermitted('employees', 'CanViewWorkRelationships');
                    }
                    tempChild.isChecked = !tempChild.hasPermission ? false : tempChild.isChecked;
                });
                tempParent.hasPartialChildChecked = hasPartialChecked(tempParent.childData[0]);
                tempParent.isParentChecked = isAllChecked(tempParent.childData[0]);
                tempParent.hasPermission = !Object.entries(tempParent.childData[0]).every((c) => !c[1].hasPermission);
                tempParent.isIncludeDocumentChecked = !tempParent.hasPermission ? false : tempParent.isIncludeDocumentChecked;
            } else if (tempParent.permissions.every((p) => permissions.props[p])) {
                tempParent.hasPermission = tempParent.permissions.every((p) => permissions.props[p].view);
                tempParent.isParentChecked = !tempParent.hasPermission ? false : tempParent.isParentChecked;
            }
        });
    };
    useEffect(async () => {
        try {
            const personnelRecordCount = await getFileCount(categoryType.personnel);
            const medicalRecordCount = await getFileCount(categoryType.medical);
            const employeePermissions = await getEmployeePermissions();
            // updating Parent Document Count
            staffProfileData.personnelDocumentSummary.totalCategoryCount = personnelRecordCount.total;
            staffProfileData.medicalDocumentSummary.totalCategoryCount = medicalRecordCount.total;
            // updating Children Document Count
            mapDocumentsCount(personnelRecordCount, categoryType.personnel);
            mapDocumentsCount(medicalRecordCount, categoryType.medical);
            mapPermissions(employeePermissions);
            setStaffProfile(staffProfileData);
        } catch (e) {
            ssToastHlp.error(e);
        } finally {
            setLoading(false);
        }
    }, []);

    useEffect(() => {
        if (staffProfile) {
            setButtonDisabled({
                selectAll: Object.values(staffProfile).every((profile) => profile.isParentChecked && ((profile.includeDocument && profile.isIncludeDocumentChecked) || !profile.includeDocument)),
                deselectAll: Object.values(staffProfile).every((profile) => !profile.isParentChecked && !profile.hasPartialChildChecked && !profile.isIncludeDocumentChecked),
                isAnySelected: !Object.values(staffProfile).some((profile) => profile.isParentChecked || profile.hasPartialChildChecked)
            });
        }
    }, [staffProfile]);

    const updateToggleState = (parent) => {
        const parentKey = parent[0];
        setStaffProfile((prev) => {
            const data = {
                ...prev,
                [parentKey]: {
                    ...prev[parentKey],
                    isIncludeDocumentChecked: !parent[1].isIncludeDocumentChecked
                }
            };
            return data;
        });
    };

    function updateChildObjects(children, updatedValue) {
        return children.map((child) => {
            const tempChild = { ...child };
            Object.keys(tempChild).filter((property) => tempChild[property].hasPermission)
                .forEach((property) => {
                    tempChild[property].isChecked = updatedValue;
                });
            return tempChild;
        });
    }
    const onChangeParent = (parent) => {
        const isParentChecked = !(parent[1].isParentChecked || parent[1].hasPartialChildChecked) && parent[1].hasPermission;
        const parentKey = parent[0];
        const updatedChildObjects = updateChildObjects(parent[1].childData, isParentChecked);
        const hasPartialChildChecked = parent[1].childData.length && hasPartialChecked(parent[1].childData[0]);
        setStaffProfile((prev) => {
            const data = {
                ...prev,
                [parentKey]: {
                    ...prev[parentKey],
                    isParentChecked,
                    hasPartialChildChecked,
                    childData: updatedChildObjects,
                    isIncludeDocumentChecked: !isParentChecked ? false : parent[1].isIncludeDocumentChecked
                }
            };
            return data;
        });
    };

    const onChangeChild = (parent, child) => {
        const childProperty = child[0];
        const childData = { ...parent[1].childData[0] };
        childData[childProperty].isChecked = !child[1].isChecked;

        const hasPartialChildChecked = hasPartialChecked(childData);
        const isAllChildrenChecked = isAllChecked(childData);
        const isAllChildrenUnchecked = isAllChecked(childData, false);

        setStaffProfile((prev) => {
            const data = {
                ...prev,
                [parent[0]]: {
                    ...prev[parent[0]],
                    isParentChecked: isAllChildrenChecked,
                    hasPartialChildChecked,
                    childData: [childData],
                    isIncludeDocumentChecked: isAllChildrenUnchecked ? false : parent[1].isIncludeDocumentChecked
                }
            };
            return data;
        });
    };

    const getStaffProfileFields = () => {
        const fields = [];
        Object.entries(staffProfile).forEach((profile) => {
            const tempParent = profile[1];
            if (tempParent.childData.length === 0) {
                fields.push({ name: tempParent.mappingName, isChecked: tempParent.isParentChecked });
            } else {
                Object.entries(tempParent.childData[0]).forEach((child) => {
                    const tempChild = child[1];
                    fields.push({ name: tempChild.mappingName, isChecked: tempChild.isChecked });
                });
            }
        });
        return fields;
    };
    const exportStaffProfileData = async (data, employeeId) => {
        let isSuccess = true;
        try {
            await ssEmployeeApi.staffProfileExport({ employeeId }, data, false);
        } catch (err) {
            isSuccess = false;
            ssToastHlp.error(err);
        }
        return isSuccess;
    };
    const exportStaffProfile = async () => {
        setExportStatus(true);
        const data = {
            fields: getStaffProfileFields(),
            IncludePersonalDocuments: staffProfile.personnelDocumentSummary.isIncludeDocumentChecked,
            IncludeMedicalDocuments: staffProfile.medicalDocumentSummary.isIncludeDocumentChecked,
        };
        const response = await exportStaffProfileData(data, $stateParams.employeeId);
        if (response) {
            if (isPermitted('DataImports', 'View_DownloadsMenuItem')) {
                routeVm.redirectToSystemJob();
                ssToastHlp.success('An export is being generated and will appear on the System Jobs page shortly.', 'Staff Profile Export in Progess');
            } else {
                routeVm.redirectToPreviousRoute();
                ssToastHlp.success('An export is being generated and you will receive an email when it is complete.', 'Staff Profile Export in Progess');
            }
        }
    };
    const toggleSelections = (isChecked) => {
        const tempStaffProfile = { ...staffProfile };
        Object.entries(tempStaffProfile).forEach((parent) => {
            parent[1].isParentChecked = !isChecked;
            parent[1].hasPartialChildChecked = false;
            if (parent[1].includeDocument) {
                parent[1].isIncludeDocumentChecked = isChecked;
            }
            onChangeParent(parent);
        });
    };

    return (
        <div className="staff-profile-container">
            <div className="header" data-test='export-heading'>
                <h3>Selections will be exported to the System Jobs page in Frontline Central</h3>
                <div className="action-buttons">
                    <SuperButton label="Cancel" onClick={() => routeVm.redirectToPreviousRoute()} data-test="cancel"></SuperButton>
                    <SuperButton className="super-button pri" disabled={isButtonDisabled.isAnySelected || isExportButton} onClick={exportStaffProfile} label="Export" data-test="export"></SuperButton>
                </div>
            </div>
            <div className="mass-select">
                <button type="button" data-test="selectAll" disabled={isButtonDisabled.selectAll} onClick={() => toggleSelections(true)}>Select All</button>
                <span className="vertical-line"></span>
                <button disabled={isButtonDisabled.deselectAll} data-test="deselectAll" type="button" onClick={() => toggleSelections(false)}>Deselect All</button>
            </div>
            <Loader fill={true} loading={isLoading} />
            {staffProfile && !isLoading
                && Object.entries(staffProfile).map((item) => (
                    <div className="parent-container" key={item[0]}>
                        <div className="parent-checkbox">
                            <div className="checkbox"><SuperInputCheckbox label={item[1].categoryLabel} labelWeight="heavy" value={item[1].isParentChecked} disabled={!item[1].hasPermission} indeterminate={item[1].hasPartialChildChecked} data-test={`parent-${item[0]}-${item[1].isParentChecked}_indeterminate-${item[1].hasPartialChildChecked}`} onChange={() => onChangeParent(item)}></SuperInputCheckbox>
                                {!item[1].hasPermission && item[1].childData.length === 0 && <Tooltip text="Permissions needed" position="top">
                                    <i className="parent-permission-icon fa-lock" data-test="permission-lock-icon" aria-label="this is non permitted field" />
                                </Tooltip>}
                                {item[1].totalCategoryCount > 0 && <label className="document-label">({`${item[1].totalCategoryCount} ${item[1].totalCategoryCount > 1 ? 'documents' : 'document'}`})</label>}</div>
                            {item[1].includeDocument && <div className="include-document"><ToggleSwitch value={item[1].isIncludeDocumentChecked} label="Include documents in export" disabled={!item[1].hasPermission} data-test={`toggle-${item[0]}-${item[1].isIncludeDocumentChecked}`} onChange={() => updateToggleState(item)} /></div>}
                        </div>
                        {item[1].childData.length > 0 && <ExportProfileItem childContentData={item} onChangeChild={onChangeChild} />}
                    </div>
                ))
            }
        </div>
    );
};

ExportProfile.propTypes = {
    $injector: PropTypes.object.isRequired,
    $stateParams: PropTypes.shape({
        employeeId: PropTypes.string
    }).isRequired,
    $scope: PropTypes.object.isRequired
};

export default ExportProfile;
