/* eslint-disable no-case-declarations */
import React from 'react';
import { logger } from 'fc-ui-shared-components';
import { ASSIGNMENT } from '../../../../constants/actionTypes';

export const AssignmentContext = React.createContext();

export const init = (position, assignment) => {
    const details = assignment._details.map((detail) => (
        {
            id: detail.id,
            fields: {},
            editModel: {}
        }
    ));
    return {
        assignment,
        details,
        editModel: {},
        isSaving: false,
        newDetails: [],
        position,
        isProcessing: false,
        showLoader: false
    };
};

export const assignmentReducer = (state, action) => {
    const { payload, type } = action;
    switch (type) {
        case ASSIGNMENT.EDIT_FIELD:
            return {
                ...state,
                editModel: { ...state.editModel, [`${payload.field}`]: payload.value }
            };
        case ASSIGNMENT.EDIT_DETAILS_FIELD:
            let detail = state.details.find((d) => d.id === payload.id);
            if (!detail) {
                detail = { id: payload.id, editModel: { [`${payload.field}`]: payload.value } };
                return {
                    ...state,
                    details: [...state.assignment._details.map((d) => ({ id: d.id, editModel: {} })).filter((d) => d.id !== payload.id), detail]
                };
            }
            detail = { ...detail, editModel: { ...detail.editModel, [`${payload.field}`]: payload.value } };
            return {
                ...state,
                details: [...state.details.filter((d) => d.id !== payload.id), detail]
            };
        case ASSIGNMENT.ADD_DETAILS:
            return {
                ...state,
                details: [{ id: payload.id, isNew: true, editModel: {} }, ...state.details]
            };
        case ASSIGNMENT.DELETE_DETAILS_FIELD:
            const foundDetail = state.details.find((d) => d.id === payload.id);
            if (foundDetail) {
                delete foundDetail.editModel[payload.field];
                return {
                    ...state,
                    details: [...state.details.filter((d) => d.id !== payload.id), foundDetail]
                };
            }
            return state;
        case ASSIGNMENT.CREATE_DETAIL:
            return {
                ...state,
                newDetails: [payload, ...state.newDetails]
            };
        case ASSIGNMENT.CANCEL:
            return {
                ...state,
                details: state.details.filter((x) => !x.isNew).map((d) => ({ ...d, deleted: false, editModel: {} })),
                editModel: {},
                newDetails: []
            };
        case ASSIGNMENT.PROCESSING:
            return {
                ...state,
                isProcessing: payload
            };
        case ASSIGNMENT.SHOWLOADER:
            return {
                ...state,
                showLoader: payload
            };
        case ASSIGNMENT.RESET_DETAIL:
            state.details.find((d) => d.id === payload.id).editModel = {};

            return {
                ...state,
                details: [...state.details]
            };
        case ASSIGNMENT.REMOVE_DETAIL:
            state.details.find((d) => d.id === payload.id).deleted = true;

            return {
                ...state,
                details: [...state.details],
                newDetails: [...state.newDetails.filter((d) => d.id !== payload.id)]
            };
        case ASSIGNMENT.SUCCESS:
            return {
                ...state,
                editModel: {},
                newDetails: []
            };
        case ASSIGNMENT.UPDATE:
            return {
                ...state,
                details: payload._details.map((d) => ({ id: d.id, editModel: {} })),
                editModel: {},
                newDetails: []
            };
        case ASSIGNMENT.EDIT_JOB_TYPE_FIELD:
            return {
                ...state,
                editModel: { ...state.editModel, jobTypeId: payload.jobTypeId },
                jobTypeChanged: true
            };
        default:
            logger.warn(`Action type: ${type} not found in assignmentReducer.`);
            return state;
    }
};
