import React from 'react';
import { logger } from 'fc-ui-shared-components';
import { SUPPLEMENTAL } from '../../../../constants/actionTypes';

export const SupplementalContext = React.createContext();

export const supplementalReducer = (state, action) => {
    const { payload, type } = action;
    switch (type) {
        case SUPPLEMENTAL.EDIT_FIELD:
            return {
                ...state,
                editModel: { ...state.editModel, [`${payload.field}`]: payload.value }
            };
        case SUPPLEMENTAL.CANCEL:
        case SUPPLEMENTAL.SUCCESS:
            return {
                ...state,
                editModel: {}
            };
        case SUPPLEMENTAL.PROCESSING:
            return {
                ...state,
                isProcessing: payload
            };
        case SUPPLEMENTAL.SHOWLOADER:
            return {
                ...state,
                showLoader: payload
            };
        default:
            logger.warn(`Action type: ${type} not found in supplementalReducer.`);
            return state;
    }
};
