import { useReducer, useContext } from 'react';
import { dateHelper, logger } from 'fc-ui-shared-components';
import {
    cancelSupplementalEdit, editField, processing, showLoader, success
} from '../../../actions/supplemental';
import { supplementalReducer } from '../components/supplemental/context';
import { AssignmentsTabContext } from '../context';

export const useSupplementalForm = (supplemental, supplementalType, contractGroup) => {
    const {
        setEditMode, supplementals, setSupplementals, $injector, $stateParams
    } = useContext(AssignmentsTabContext);
    const [state, dispatch] = useReducer(supplementalReducer, {
        supplemental, editModel: {}, isProcessing: false, showLoader: false
    });

    const getStartDate = (value) => (value == null || value === dateHelper.foreverPast ? dateHelper.foreverPast : value);
    const getEndDate = (value) => (value == null || value === dateHelper.foreverFuture ? dateHelper.foreverFuture : value);

    const modalSvc = $injector.get('ssModalSvc');
    const toastHlp = $injector.get('ssToastHlp');

    const fields = {
        externalId: Object.prototype.hasOwnProperty.call(state.editModel, 'externalId') ? state.editModel.externalId : supplemental._externalId,
        totalInstallments: Object.prototype.hasOwnProperty.call(state.editModel, 'totalInstallments') ? state.editModel.totalInstallments : supplemental._totalInstallments,
        totalPayAmount: Object.prototype.hasOwnProperty.call(state.editModel, 'totalPayAmount') ? state.editModel.totalPayAmount : supplemental._totalPayAmount,
        contractGroupId: Object.prototype.hasOwnProperty.call(state.editModel, 'contractGroupId') ? state.editModel.contractGroupId : contractGroup && contractGroup.value,
        supplementalId: Object.prototype.hasOwnProperty.call(state.editModel, 'supplementalId') ? state.editModel.supplementalId : supplementalType && supplementalType.id,
        startDateUtc: Object.prototype.hasOwnProperty.call(state.editModel, 'startDateUtc') ? state.editModel.startDateUtc : getStartDate(supplemental._startDateUtc),
        endDateUtc: Object.prototype.hasOwnProperty.call(state.editModel, 'endDateUtc') ? state.editModel.endDateUtc : getEndDate(supplemental._endDateUtc)
    };

    const setField = (field, value) => { dispatch(editField(field, value)); };

    const cancel = () => {
        dispatch(cancelSupplementalEdit());
        setEditMode(null);
    };

    const save = () => {
        dispatch(processing());
        supplemental.$refresh().then(() => {
            supplemental._externalId = fields.externalId;
            supplemental._totalInstallments = fields.totalInstallments;
            supplemental._totalPayAmount = fields.totalPayAmount;
            supplemental._contractGroupId = fields.contractGroupId;
            supplemental._startDateUtc = getStartDate(fields.startDateUtc);
            supplemental._endDateUtc = getEndDate(fields.endDateUtc);
            supplemental._supplementalId = fields.supplementalId;
            supplemental.$save(true, true, false)
                .then(() => {
                    toastHlp.success(`"${supplementalType._name}" has been saved`);
                    dispatch(success());
                }).catch((err) => {
                    if (err.data.errors) {
                        toastHlp.errorAjax(err);
                    }
                    logger.error(err);
                }).finally(() => {
                    setEditMode(null);
                    dispatch(processing(false));
                });
        });
        supplemental.recentlyUpdated = true;
    };

    const deleteSupplemental = () => {
        modalSvc.showDestructive({
            title: 'Are you sure you want to delete this Supplemental?',
            primaryText: 'Delete',
            secondaryText: 'Cancel',
        }).then(() => {
            dispatch(showLoader(true));
            supplemental.$remove().then(() => {
                setSupplementals(supplementals.filter((supp) => supp.id !== supplemental.id));
                dispatch(success());
                setEditMode(null);
                toastHlp.success(`"${supplementalType._name}" has been deleted`);
            }).catch((err) => {
                toastHlp.error(err, `There was an error deleting "${supplementalType._name}"`);
                logger.error(err);
            }).finally(() => {
                dispatch(showLoader(false));
            });
        });
    };

    const create = () => {
        const empApi = $injector.get('ssEmployeeApi');
        dispatch(processing());
        const origialSupplementals = supplementals.filter((s) => s.id !== null);
        empApi.createEmployeeSupplemental({ id: $stateParams.employeeId }, fields, false)
            .then((res) => {
                if (res) {
                    res.recentlyUpdated = true;
                    origialSupplementals.push(res);
                }
                setSupplementals(origialSupplementals);
                dispatch(success());
            }).catch((err) => {
                if (err.data.errors) {
                    toastHlp.errorAjax(err);
                }
                logger.error(err);
            }).finally(() => {
                dispatch(processing(false));
                setEditMode(null);
            });
    };

    const cancelCreate = () => {
        setSupplementals(supplementals.filter((s) => s.id !== null));
        setEditMode(null);
    };

    return {
        fields,
        setField,
        cancel,
        save,
        create,
        deleteSupplemental,
        cancelCreate,
        isProcessing: state.isProcessing,
        showLoader: state.showLoader
    };
};
