import { useContext, useReducer } from 'react';
import moment from 'moment';
import {
    jobTypeHelper,
    employeeAssignmentHelper,
    logger,
    dateHelper,
    dateByOrgHelper
} from 'fc-ui-shared-components';
import { AssignmentsTabContext } from '../context';
import { assignmentReducer, init } from '../components/assignment/context';
import {
    editField,
    editDetailsField,
    cancelAssignmentEdit,
    processing,
    showLoader,
    success,
    addDetails,
    createDetail,
    removeDetail,
    resetDetail,
    updateAssignment,
    deleteDetailsField,
    editJobTypeField
} from '../../../actions/assignment';

export const useAssignmentForm = (position, assignment, name) => {
    const {
        state: tabState, $injector, $stateParams, setEditMode, setAddMode, positions, setPositions, refreshPositions, organization, positionTypes
    } = useContext(AssignmentsTabContext);
    const toastHlp = $injector.get('ssToastHlp');
    const [state, dispatch] = useReducer(assignmentReducer, init(position, assignment));
    const setField = (field, value) => dispatch(editField(field, value));
    const setDetailsField = (id, field, value) => dispatch(editDetailsField(id, field, value));
    const deleteDetailField = (id, field) => dispatch(deleteDetailsField(id, field));
    const setAssignment = (value) => dispatch(updateAssignment(value));
    const setJobTypeField = (id) => dispatch(editJobTypeField(id));

    const getStartDate = (value) => (value == null || value === dateHelper.foreverPast ? dateHelper.foreverPast : value);
    const getEndDate = (value) => (value == null || value === dateHelper.foreverFuture ? dateHelper.foreverFuture : value);

    const fields = {
        externalId: Object.prototype.hasOwnProperty.call(state.editModel, 'externalId') ? state.editModel.externalId : assignment._externalId,
        startDate: Object.prototype.hasOwnProperty.call(state.editModel, 'startDate') ? state.editModel.startDate : getStartDate(position._startDate),
        endDate: Object.prototype.hasOwnProperty.call(state.editModel, 'endDate') ? state.editModel.endDate : getEndDate(position._endDate),
        name: Object.prototype.hasOwnProperty.call(state.editModel, 'name') ? state.editModel.name : name,
        jobTypeId: Object.prototype.hasOwnProperty.call(state.editModel, 'jobTypeId') ? state.editModel.jobTypeId : position._jobType.id,
        contractGroupId: Object.prototype.hasOwnProperty.call(state.editModel, 'contractGroupId') ? state.editModel.contractGroupId : assignment._contractGroupId,
        locationId: Object.prototype.hasOwnProperty.call(state.editModel, 'locationId') ? state.editModel.locationId : position._location.id,
        departmentId: Object.prototype.hasOwnProperty.call(state.editModel, 'departmentId') ? state.editModel.departmentId : position._department.id
    };

    const deleteAssignmentDetail = (id) => dispatch(removeDetail(id));

    const resetAssignmentDetail = (id) => dispatch(resetDetail(id));

    const getJobTypeDetails = (detail, jobType) => (jobType.id && detail._fromDateUtc ? jobTypeHelper.getJobTypeDetail({
        startDate: detail._fromDateUtc,
        endDate: detail._toDateUtc,
        jobType,
        org: organization
    }) : {});

    const getPaySchedule = (detail, jobType) => (jobType.id && detail._fromDateUtc ? jobTypeHelper.getPaySchedule({
        startDate: detail._fromDateUtc,
        endDate: detail._toDateUtc,
        jobType,
        org: organization
    }) : {});

    const getValidPayRate = (paySchedule, detail, formDetail) => {
        let currentDetails;
        const editModelPayLanes = Object.prototype.hasOwnProperty.call(formDetail.editModel, 'payLane')
            || Object.prototype.hasOwnProperty.call(formDetail.editModel, 'payStep');
        const fieldDataPayLanes = Object.prototype.hasOwnProperty.call(formDetail.fields, 'payLane')
            && Object.prototype.hasOwnProperty.call(formDetail.fields, 'payStep');
        const payRate = Object.prototype.hasOwnProperty.call(formDetail.editModel, 'payRate')
            ? formDetail.editModel.payRate
            : detail._payRate;
        // If we don't have an edit model of some sort around payLane and payStep return what we got from the server
        if ((!editModelPayLanes && !fieldDataPayLanes && payRate !== null) || (fieldDataPayLanes && payRate !== null)) {
            return payRate;
        }
        if (fieldDataPayLanes || editModelPayLanes) {
            currentDetails = {
                _payLane: Object.prototype.hasOwnProperty.call(formDetail.editModel, 'payLane') ? formDetail.editModel.payLane : formDetail.fields.payLane,
                _payStep: Object.prototype.hasOwnProperty.call(formDetail.editModel, 'payStep') ? formDetail.editModel.payStep : formDetail.fields.payStep
            };
            return employeeAssignmentHelper.getPayRate({ paySchedule, currentDetails });
        }
        return 0;
    };

    const getDefaultPayRate = (paySchedule, formDetail) => {
        let defaultRate = 0;
        if (!paySchedule || Object.keys(paySchedule).length === 0) return defaultRate;
        if (formDetail.fields?.payLane !== '' && formDetail.fields?.payStep !== '') {
            const currentDetails = {
                _payLane: (Object.prototype.hasOwnProperty.call(formDetail.editModel, 'payLane') && formDetail.editModel.payLane !== '') ? formDetail.editModel.payLane : formDetail.fields.payLane,
                _payStep: (Object.prototype.hasOwnProperty.call(formDetail.editModel, 'payStep') && formDetail.editModel.payStep !== '') ? formDetail.editModel.payStep : formDetail.fields.payStep
            };
            defaultRate = employeeAssignmentHelper.getPayRate({ paySchedule, currentDetails });
        }
        return defaultRate;
    };

    const setDetailsFields = (detail, formDetail, jobType) => {
        if (!formDetail.fields) {
            formDetail.fields = {};
        }
        const derivedDetail = { _fromDateUtc: formDetail.fields.startDate || detail._fromDateUtc, _toDateUtc: formDetail.fields.endDate || detail._toDateUtc };
        const jobTypeDetails = getJobTypeDetails(derivedDetail, jobType);
        const paySchedule = getPaySchedule(detail, jobType);
        const payScheduleIsValid = jobTypeHelper.isValidPS(paySchedule);

        const resolveJobTypeField = (field) => {
            if (detail.isNew || Object.prototype.hasOwnProperty.call(state.editModel, 'jobTypeId')) return typeof jobTypeDetails[field] !== 'undefined' ? jobTypeDetails[field] : detail[field];
            return (detail[field] || detail[field] === 0) ? detail[field] : jobTypeDetails[field];
        };

        const jobTypeAssignmentValues = employeeAssignmentHelper.jobTypeValuesForAssignment({
            paySchedule,
            jobTypeDetails,
            assignmentDetails: detail,
        });

        let payRateFrequency = payScheduleIsValid
            ? jobTypeAssignmentValues._payRateFrequency
            : detail._payRateFrequency;
        if (Object.prototype.hasOwnProperty.call(formDetail.editModel, 'payRateFrequency')) {
            payRateFrequency = formDetail.editModel.payRateFrequency;
        }

        const payRate = Object.prototype.hasOwnProperty.call(formDetail.editModel, 'payRate') ? formDetail.editModel.payRate : getValidPayRate(paySchedule, detail, formDetail);

        const payOverridePayRateFrequency = Object.prototype.hasOwnProperty.call(formDetail.editModel, 'payOverridePayRateFrequency') ? formDetail.editModel.payOverridePayRateFrequency : (detail._payOverride?.payRateFrequency ?? payRateFrequency);

        // use override value for pay type if it exists
        const currentPayRateFrequencyValue = (payOverridePayRateFrequency || payOverridePayRateFrequency === 0) ? payOverridePayRateFrequency : (payRateFrequency || 0);

        const hoursPerDay = Object.prototype.hasOwnProperty.call(formDetail.editModel, 'hoursPerDay') ? formDetail.editModel.hoursPerDay : resolveJobTypeField('_hoursPerDay');

        const workDaysPerYear = Object.prototype.hasOwnProperty.call(formDetail.editModel, 'workDaysPerYear') ? formDetail.editModel.workDaysPerYear : resolveJobTypeField('_workDaysPerYear');

        const payPeriodsPerYear = Object.prototype.hasOwnProperty.call(formDetail.editModel, 'payPeriodsPerYear') ? formDetail.editModel.payPeriodsPerYear : resolveJobTypeField('_payPeriodsPerYear');

        const payPerPeriod = employeeAssignmentHelper.calculatePayPerPeriod({
            annualPay: employeeAssignmentHelper.calculateAnnualPay({
                payRateFrequency: currentPayRateFrequencyValue,
                payRate,
                hoursPerDay,
                workDaysPerYear
            }),
            payPeriodsPerYear,
            payRateFrequency: currentPayRateFrequencyValue
        });

        const detailFields = {
            payScheduleName: detail._paySchedule ? detail._paySchedule : paySchedule && paySchedule._name,
            payLane: Object.prototype.hasOwnProperty.call(formDetail.editModel, 'payLane') ? formDetail.editModel.payLane : detail._payLane,
            payStep: Object.prototype.hasOwnProperty.call(formDetail.editModel, 'payStep') ? formDetail.editModel.payStep : detail._payStep,
            payRate,

            payOverridePaySchedule: Object.prototype.hasOwnProperty.call(formDetail.editModel, 'payOverridePaySchedule') ? formDetail.editModel.payOverridePaySchedule : detail._payOverride?.paySchedule || '',
            payOverridePayLane: Object.prototype.hasOwnProperty.call(formDetail.editModel, 'payOverridePayLane') ? formDetail.editModel.payOverridePayLane : detail._payOverride?.payLane || '',
            payOverridePayStep: Object.prototype.hasOwnProperty.call(formDetail.editModel, 'payOverridePayStep') ? formDetail.editModel.payOverridePayStep : detail._payOverride?.payStep || '',
            payOverridePayRateFrequency,

            defaultPayRate: getDefaultPayRate(paySchedule, formDetail),
            endDate: Object.prototype.hasOwnProperty.call(formDetail.editModel, 'endDate') ? formDetail.editModel.endDate : detail._toDateUtc,
            fTE: Object.prototype.hasOwnProperty.call(formDetail.editModel, 'fTE') ? formDetail.editModel.fTE : resolveJobTypeField('_fTE'),
            hoursPerDay,
            payPeriodsPerYear,
            payPerPeriod,
            payScheduleIsValid,
            usePayOverride: Object.prototype.hasOwnProperty.call(formDetail.editModel, 'usePayOverride') ? formDetail.editModel.usePayOverride : detail._usePayOverride,
            payRateFrequency,
            payRateFrequencyString: employeeAssignmentHelper.getPayRateFrequencyString(currentPayRateFrequencyValue),
            personalDaysPerSchoolYear: Object.prototype.hasOwnProperty.call(formDetail.editModel, 'personalDaysPerSchoolYear') ? formDetail.editModel.personalDaysPerSchoolYear : resolveJobTypeField('_personalDaysPerSchoolYear'),
            sickDaysPerSchoolYear: Object.prototype.hasOwnProperty.call(formDetail.editModel, 'sickDaysPerSchoolYear') ? formDetail.editModel.sickDaysPerSchoolYear : resolveJobTypeField('_sickDaysPerSchoolYear'),
            startDate: Object.prototype.hasOwnProperty.call(formDetail.editModel, 'startDate') ? formDetail.editModel.startDate : detail._fromDateUtc,
            vacationDaysPerSchoolYear: Object.prototype.hasOwnProperty.call(formDetail.editModel, 'vacationDaysPerSchoolYear') ? formDetail.editModel.vacationDaysPerSchoolYear : resolveJobTypeField('_vacationDaysPerSchoolYear'),
            workDaysPerYear,
        };

        return detailFields;
    };

    const setAssignmentDetailEndDate = (assignmentDetailEndDate, assignmentEndDate) => {
        const detailEndDate = getEndDate(assignmentDetailEndDate);
        // Setting enddate in Case : Assignment have date and details does not.
        if (dateHelper.isForeverFuture(detailEndDate)) {
            return dateHelper.isForeverFuture(assignmentEndDate) ? detailEndDate : assignmentEndDate;
        }
        return detailEndDate;
    };

    const cancel = () => {
        dispatch(cancelAssignmentEdit());
        setEditMode(null);
        setAddMode(null);
    };

    const resolvePayRateToSave = (id, form, details, jobType) => {
        const detail = details.find((d) => d.id === id) ?? { ...form.fields, _fromDateUtc: form.fields.startDate, _toDateUtc: form.fields.endDate };
        const paySchedule = getPaySchedule(detail, jobType);
        const currentDetails = {
            _payLane: form.fields.payLane,
            _payStep: form.fields.payStep
        };
        const payRate = employeeAssignmentHelper.getPayRate({ paySchedule, currentDetails });
        return form.fields.payRate !== payRate ? form.fields.payRate : null;
    };

    const setPayOverride = (detail, detailForm) => {
        if (!detail._usePayOverride) {
            detail._payOverride = null;
        } else {
            Object.assign(detail._payOverride, {
                paySchedule: detailForm.fields.payOverridePaySchedule,
                payLane: detailForm.fields.payOverridePayLane,
                payStep: detailForm.fields.payOverridePayStep,
                payRateFrequency: detailForm.fields.payOverridePayRateFrequency
            });
        }
    };

    const resolveUsePayOverride = (form) => {
        const textOverrideFields = ['payOverridePaySchedule', 'payOverridePayLane', 'payOverridePayStep'];
        const hasTextFieldValues = textOverrideFields.some((x) => form.fields[`${x}`] !== '');
        if (!form.fields.payScheduleIsValid) {
            return form.fields.usePayOverride || hasTextFieldValues;
        }
        return form.fields.usePayOverride || hasTextFieldValues || form.fields.payOverridePayRateFrequency !== form.fields.payRateFrequency;
    };

    const save = async (jobType) => {
        dispatch(processing());
        const lastKnownEndDate = position._endDate;
        const lastKnownExternalId = position.assignment._externalId;
        try {
            await position.$refresh();

            position._name = fields.name;
            position._startDate = getStartDate(fields.startDate);
            position._endDate = getEndDate(fields.endDate);
            position.assignment._startDateUtc = getStartDate(fields.startDate);
            position.assignment._endDateUtc = getEndDate(fields.endDate);
            position.assignment._externalId = fields.externalId ? fields.externalId : position.id;
            position._jobType.id = fields.jobTypeId;
            position.assignment._contractGroupId = fields.contractGroupId;
            position._location.id = fields.locationId;
            position._department.id = fields.departmentId;

            await position.$save();

            const existingDetails = assignment._details.filter((x) => !x.isNew);
            existingDetails.forEach(async (detail) => {
                const jobTypeDetails = getJobTypeDetails(detail, jobType);
                const form = state.details.find((d) => d.id === detail.id);
                const resolveJobTypeDetailToSave = (fieldname) => (form.fields[fieldname] !== jobTypeDetails[`_${fieldname}`] ? form.fields[fieldname] : null);

                if (!form || Object.keys(form.editModel).length === 0) return;
                detail._fromDateUtc = form.fields.startDate;
                detail._usePayOverride = resolveUsePayOverride(form);
                setPayOverride(detail, form);
                detail._toDateUtc = setAssignmentDetailEndDate(form.fields.endDate, fields.endDate);
                detail._payLane = form.fields.payLane;
                detail._payStep = form.fields.payStep;
                detail._payRate = resolvePayRateToSave(detail.id, form, existingDetails, jobType);
                detail._payRateFrequency = resolveJobTypeDetailToSave('payRateFrequency');
                detail._fTE = resolveJobTypeDetailToSave('fTE');
                detail._hoursPerDay = resolveJobTypeDetailToSave('hoursPerDay');
                detail._payPeriodsPerYear = resolveJobTypeDetailToSave('payPeriodsPerYear');
                detail._personalDaysPerSchoolYear = resolveJobTypeDetailToSave('personalDaysPerSchoolYear');
                detail._sickDaysPerSchoolYear = resolveJobTypeDetailToSave('sickDaysPerSchoolYear');
                detail._vacationDaysPerSchoolYear = resolveJobTypeDetailToSave('vacationDaysPerSchoolYear');
                detail._workDaysPerYear = resolveJobTypeDetailToSave('workDaysPerYear');
                await detail.$save();
            });

            let assignmentData = await $injector.get('ssAssignmentApi').getAssignment({
                id: position.id,
                assignmentId: assignment.id,
                include: 'details'
            });
            const detailsRef = [...assignmentData._details];
            assignmentData._details.length = 0;
            state.details.filter((detail) => !detail.deleted).forEach((form) => {
                const jobTypeDetails = getJobTypeDetails({ _fromDateUtc: form.fields.startDate, _toDateUtc: form.fields.endDate }, jobType);
                const resolveJobTypeDetailToSave = (fieldname) => (form.fields[fieldname] !== jobTypeDetails[`_${fieldname}`] ? form.fields[fieldname] : null);

                const newModel = {
                    _fromDateUtc: form.fields.startDate,
                    _toDateUtc: setAssignmentDetailEndDate(form.fields.endDate, fields.endDate),
                    _payLane: form.fields.payLane,
                    _usePayOverride: resolveUsePayOverride(form),
                    _payOverride: {},
                    _payStep: form.fields.payStep,
                    _payRate: resolvePayRateToSave(form.id, form, detailsRef, jobType),
                    _payRateFrequency: resolveJobTypeDetailToSave('payRateFrequency'),
                    _fTE: resolveJobTypeDetailToSave('fTE'),
                    _hoursPerDay: resolveJobTypeDetailToSave('hoursPerDay'),
                    _payPeriodsPerYear: resolveJobTypeDetailToSave('payPeriodsPerYear'),
                    _personalDaysPerSchoolYear: resolveJobTypeDetailToSave('personalDaysPerSchoolYear'),
                    _sickDaysPerSchoolYear: resolveJobTypeDetailToSave('sickDaysPerSchoolYear'),
                    _vacationDaysPerSchoolYear: resolveJobTypeDetailToSave('vacationDaysPerSchoolYear'),
                    _workDaysPerYear: resolveJobTypeDetailToSave('workDaysPerYear')
                };
                setPayOverride(newModel, form);
                assignmentData._details.push(newModel);
            });

            await assignmentData._details.$save({ opOrder: ['remove', 'modify', 'add'] });

            assignmentData = await $injector.get('ssAssignmentApi').getAssignment({
                id: position.id,
                assignmentId: assignment.id,
                include: 'details'
            });
            position.assignment = assignmentData;
            const positionType = positionTypes.find((p) => p.value === name);
            toastHlp.success(`"${positionType.name}" has been saved`);
            setAssignment(assignmentData);
            dispatch(success());
        } catch (err) {
            setField('endDate', lastKnownEndDate);
            position._endDate = lastKnownEndDate;
            setField('externalId', lastKnownExternalId);
            position.assignment._externalId = lastKnownExternalId;
            logger.error(err);
        } finally {
            dispatch(processing(false));
            setEditMode(null);
        }
    };

    const addNewDetails = (newDetail) => {
        dispatch(addDetails(newDetail.id));
        dispatch(createDetail(newDetail));
    };

    const create = async (jobType) => {
        dispatch(processing());
        const isPrimary = tabState.adding === 'primary';

        const newAssignment = {
            id: null,
            _isPrimary: isPrimary,
            _externalId: fields.externalId,
            _contractGroupId: fields.contractGroupId,
            _startDateUtc: getStartDate(fields.startDate),
            _endDateUtc: getEndDate(fields.endDate),
            _employee: {
                id: $stateParams.employeeId
            },
            _details: []
        };

        const newPosition = {
            id: null,
            _name: fields.name,
            _startDate: getStartDate(fields.startDate),
            _endDate: getEndDate(fields.endDate),
            assignment: newAssignment,
            _assignments: [
                newAssignment
            ],
            _jobType: {
                id: fields.jobTypeId
            },
            _location: {
                id: fields.locationId
            },
            _department: {
                id: fields.departmentId
            }
        };

        const checkIfEditModelExists = (details) => details.deleted || (Object.keys(details.editModel).length === 0 && Object.getPrototypeOf(details.editModel) === Object.prototype);

        const saveNewAssignment = async () => {
            try {
                positions.push(newPosition);
                await positions.$save();
                const positionResult = await $injector.get('ssPositionsApi').get({
                    id: positions[positions.length - 1].id,
                    include: 'assignments'
                });
                const assignmentResult = await $injector.get('ssAssignmentApi').getAssignment({
                    id: positions[positions.length - 1].id,
                    assignmentId: positions[positions.length - 1]._assignments[0].id,
                    include: 'details'
                });

                positions.pop();
                positionResult._assignments = [assignmentResult];
                positionResult.assignment = assignmentResult;

                positions.push(positionResult);

                if (state.details.some((x) => !checkIfEditModelExists(x))) {
                    const detailsRef = [...assignmentResult._details];
                    assignmentResult._details.length = 0;
                    state.details.forEach((form) => {
                        const jobTypeDetails = getJobTypeDetails({ _fromDateUtc: form.fields.startDate, _toDateUtc: form.fields.endDate }, jobType);
                        const resolveJobTypeDetailToSave = (fieldname) => (form.fields[fieldname] !== jobTypeDetails[`_${fieldname}`] ? form.fields[fieldname] : null);

                        const newModel = {
                            _id: null,
                            _fromDateUtc: form.fields.startDate,
                            _toDateUtc: setAssignmentDetailEndDate(form.fields.endDate, fields.endDate),
                            _usePayOverride: resolveUsePayOverride(form),
                            _payOverride: {},
                            _payLane: form.fields.payLane,
                            _payStep: form.fields.payStep,
                            _payRate: resolvePayRateToSave(null, form, detailsRef, jobType),
                            _payRateFrequency: resolveJobTypeDetailToSave('payRateFrequency'),
                            _fTE: resolveJobTypeDetailToSave('fTE'),
                            _hoursPerDay: resolveJobTypeDetailToSave('hoursPerDay'),
                            _payPeriodsPerYear: resolveJobTypeDetailToSave('payPeriodsPerYear'),
                            _personalDaysPerSchoolYear: resolveJobTypeDetailToSave('personalDaysPerSchoolYear'),
                            _sickDaysPerSchoolYear: resolveJobTypeDetailToSave('sickDaysPerSchoolYear'),
                            _vacationDaysPerSchoolYear: resolveJobTypeDetailToSave('vacationDaysPerSchoolYear'),
                            _workDaysPerYear: resolveJobTypeDetailToSave('workDaysPerYear')
                        };
                        setPayOverride(newModel, form);
                        assignmentResult._details.push(newModel);
                    });
                    await assignmentResult._details.$save({ opOrder: ['remove', 'modify', 'add'] });
                }
                const createdAssignment = await $injector.get('ssAssignmentApi').getAssignment({
                    id: positions[positions.length - 1].id,
                    assignmentId: positions[positions.length - 1]._assignments[0].id,
                    include: 'details'
                });
                positions[positions.length - 1].assignment = createdAssignment;
                const positionType = positionTypes.find((p) => p.value === positions[positions.length - 1]._name);
                await refreshPositions();
                toastHlp.success(`"${positionType.name}" has been created`);
                dispatch(success());
            } catch (err) {
                await refreshPositions();
                logger.error(err);
            } finally {
                setEditMode(null);
                setAddMode(null);
                dispatch(processing(false));
            }
        };

        if (isPrimary) {
            const detailsToUpdate = [];
            const positionsToUpdate = [];
            const autoEndDate = dateByOrgHelper.endOfDayRelativeTo(moment(newPosition._startDate).add(-1, 'days').endOf('day'), organization);
            positions.filter((p) => p.assignment && p.assignment._isPrimary && p.id !== null && dateHelper.isForeverFuture(p._endDate)).forEach((p) => {
                p._endDate = autoEndDate;
                p.assignment._details.filter((d) => dateHelper.isForeverFuture(d._toDateUtc)).forEach((cd) => {
                    cd._toDateUtc = autoEndDate;
                    detailsToUpdate.push(p.assignment._details);
                });
                positionsToUpdate.push(p);
            });

            detailsToUpdate.map(async (d) => {
                await d.$save();
            });
            positionsToUpdate.map(async (p) => {
                await p.$save();
            });
        }

        await saveNewAssignment();
    };

    const deleteAssignment = async () => {
        const positionType = positionTypes.find((p) => p.value === name);
        let confirm = false;
        try {
            const modalSvc = $injector.get('ssModalSvc');
            confirm = await modalSvc.showDestructive({
                title: 'Are you sure?',
                content: 'Are you sure you want to delete this assignment?',
                primaryText: 'Delete Assignment',
                secondaryText: 'Cancel'
            });
        } catch (err) {
            setEditMode(null);
        }

        if (confirm) {
            try {
                dispatch(showLoader(true));

                await position.$remove();
                const index = positions.findIndex((p) => p.id === position.id);
                if (index >= 0) {
                    positions.splice(index, 1);
                }
                setPositions(positions);
                dispatch(success());
                toastHlp.success(`"${positionType.name}" has been deleted`);
            } catch (err) {
                toastHlp.error(err, `There was an error deleting "${positionType.name}"`);
                logger.error(err);
            } finally {
                setEditMode(null);
                dispatch(showLoader(false));
            }
        }
    };

    return {
        details: state.details,
        addNewDetails,
        newDetails: state.newDetails,
        setDetailsField,
        setDetailsFields,
        getValidPayRate,
        setJobTypeField,
        fields,
        setField,
        cancel,
        deleteAssignment,
        deleteAssignmentDetail,
        resetAssignmentDetail,
        deleteDetailField,
        save,
        create,
        isProcessing: state.isProcessing,
        showLoader: state.showLoader
    };
};
