import React, { useContext, useState, useEffect } from 'react';
import PropTypes from 'prop-types';
import { FormProvider, useForm } from 'react-hook-form';
import classNames from 'classnames';
import moment from 'moment';
import {
    ArticleCard,
    Header,
    MetaDataGrid,
    SuperActionMenu,
    SuperActionMenuItem,
    SuperButton,
    Loader,
    workRelationshipType,
    DateField,
    SelectFilterList,
    SelectList,
    dateByOrgHelper,
    logger
} from 'fc-ui-shared-components';

import { EmploymentTabContext } from '../../../context';
import { GeneralTabContext } from '../../context';
import workRelationshipApi from '../../../../../api/workRelationshipApi';
import { WorkRelationshipContext } from './context';

const WorkRelationship = ({ workRelationship }) => {
    const [isProcessing, setProcessing] = useState(false);
    const [showLoader, setShowLoader] = useState(false);
    const {
        workRelationshipType: workRelationshipTypeName, employmentGroupId, offerAcceptedDate, startDate, boardApprovedDate
    } = workRelationship;
    const {
        $injector,
        $stateParams,
        featureFlags,
        refreshEmployee,
        org
    } = useContext(EmploymentTabContext);

    const {
        workRelationshipPermissions,
        employmentGroups,
        adding,
        editing,
        setEditing,
        setAdding
    } = useContext(GeneralTabContext);

    const {
        validNewRelationshipTypes,
        cancelNewWorkRelationship,
    } = useContext(WorkRelationshipContext);

    const derivedState = {
        isNew: workRelationship.id === null,
        isEditing: editing === workRelationship.id || workRelationship.id === null,
        editingDisabled: editing !== null || adding,
        canEditWorkRelationships: workRelationshipPermissions.permittedActions.Edit,
        canDeleteWorkRelationships: workRelationshipPermissions.permittedActions.Delete,
        canViewStaffType: workRelationshipPermissions.permittedPublishedProperties.StaffType >= 2,
        canEditStaffType: workRelationshipPermissions.permittedPublishedProperties.StaffType === 3
    };

    const toastHlp = $injector.get('ssToastHlp');

    const methods = useForm({
        defaultValues: {
            workRelationshipType,
            employmentGroupId,
            boardApprovedDate,
            offerAcceptedDate,
            startDate
        }
    });

    const { isValid } = methods.formState;

    useEffect(() => {
        methods.reset({
            employmentGroupId,
            boardApprovedDate,
            offerAcceptedDate,
            startDate
        });
    }, [employmentGroupId, boardApprovedDate, offerAcceptedDate, startDate]);

    const getEmploymentGroupName = () => {
        if (employmentGroupId) {
            const group = employmentGroups.find((grp) => grp.value === employmentGroupId);
            if (group) {
                return group.name;
            }
        }
        return null;
    };

    const getWorkRelationshipTypeAndGroupName = () => {
        if (derivedState.isNew) return 'New Work Relationship';
        const groupName = derivedState.canViewStaffType ? getEmploymentGroupName() : null;
        return groupName ? `${workRelationship.workRelationshipType} - ${groupName}` : workRelationshipTypeName;
    };

    // eslint-disable-next-line no-promise-executor-return
    const delay = (ms) => new Promise((resolve) => setTimeout(resolve, ms));

    const createWorkRelationship = async () => {
        setProcessing(true);
        const formValues = methods.getValues();
        const newWorkRelationship = {
            ...formValues,
            boardApprovedDate: formValues.boardApprovedDate && dateByOrgHelper.startOfDayRelativeTo(moment(formValues.boardApprovedDate).startOf('day'), org),
            offerAcceptedDate: formValues.offerAcceptedDate && dateByOrgHelper.startOfDayRelativeTo(moment(formValues.offerAcceptedDate).startOf('day'), org),
            startDate: formValues.startDate && dateByOrgHelper.startOfDayRelativeTo(moment(formValues.startDate).startOf('day'), org)
        };

        try {
            const newWorkRel = {
                ...newWorkRelationship,
                employeeId: $stateParams.employeeId,
                organizationId: org._organizationId,
                workRelationshipType: newWorkRelationship.workRelationshipTypeName,
                boardApprovedDate: formValues.boardApprovedDate ? moment(formValues.boardApprovedDate).startOf('day').format('YYYY-MM-DD') : null,
                offerAcceptedDate: formValues.offerAcceptedDate ? moment(formValues.offerAcceptedDate).startOf('day').format('YYYY-MM-DD') : null,
                startDate: formValues.startDate ? moment(formValues.startDate).startOf('day').format('YYYY-MM-DD') : null
            };

            await workRelationshipApi.create(newWorkRel);
            await delay(1000);
            await refreshEmployee();
            toastHlp.success('Work Relationship created successfully');
        } catch (ex) {
            toastHlp.error('There was a problem creating work relationship');
        } finally {
            setAdding(false);
            setEditing(null);
            setProcessing(false);
        }
    };

    const saveWorkRelationship = async () => {
        setProcessing(true);
        const formValues = methods.getValues();
        try {
            await workRelationship.$refresh();

            workRelationship.employmentGroupId = formValues.employmentGroupId;
            workRelationship.boardApprovedUtc = formValues.boardApprovedUtc && dateByOrgHelper.startOfDayRelativeTo(moment(formValues.boardApprovedUtc).startOf('day'), org);
            workRelationship.offerAcceptedUtc = formValues.offerAcceptedUtc && dateByOrgHelper.startOfDayRelativeTo(moment(formValues.offerAcceptedUtc).startOf('day'), org);
            workRelationship.startDateUtc = formValues.startDateUtc && dateByOrgHelper.startOfDayRelativeTo(moment(formValues.startDateUtc).startOf('day'), org);
            // TODO: CHANGE THIS CALL TO USE NEW SERVICE
            // await workRelationship.$save();
            await refreshEmployee();
            toastHlp.success(`"${getWorkRelationshipTypeAndGroupName()}" has been saved`);
        } catch (err) {
            logger.error(err);
        } finally {
            setProcessing(false);
            setEditing(null);
        }
    };

    const removeWorkRelationship = async () => {
        setEditing('removing');
        let confirm = false;

        try {
            const modalSvc = $injector.get('ssModalSvc');
            confirm = await modalSvc.showDestructive({
                title: 'Are you sure?',
                content: 'Are you sure you want to delete this work relationship?',
                primaryText: 'Delete Work Relationship',
                secondaryText: 'Cancel'
            });
        } catch {
            // remove cancelled
            setEditing(null);
        }
        if (confirm) {
            setShowLoader(true);
            try {
                await workRelationship.$remove();
                await delay(1000);
                await refreshEmployee();
                toastHlp.success(`"${getWorkRelationshipTypeAndGroupName()}" has been deleted`);
            } catch (err) {
                logger.error(err);
            } finally {
                setShowLoader(false);
                setEditing(null);
            }
        }
    };

    const handleCancel = async () => {
        methods.clearErrors();
        methods.reset();
        if (derivedState.isNew) { cancelNewWorkRelationship(); }
        setEditing(null);
    };

    const validateSelectedRelationshipType = () => {
        const isValidSelection = (val) => {
            if (validNewRelationshipTypes.some((x) => x.name === val)) return true;
            let error;
            switch (parseInt(val, 10)) {
                case 0:
                    error = 'Staff member already has Employee work relationship; cannot have more than one of the same kind';
                    break;
                case 1:
                    error = 'Staff member already has Substitute Employee work relationship; cannot have more than one of the same kind';
                    break;
                default:
                    error = 'If staff member has an Employee and/or Substitute Employee work relationship; they cannot also be a Contingent Worker';
                    break;
            }
            return error;
        };
        return {
            validate: {
                isValidSelection
            }
        };
    };

    return (
        <section>
            <FormProvider {...methods} >
                <form onSubmit={methods.handleSubmit(derivedState.isNew ? createWorkRelationship : saveWorkRelationship)} noValidate>
                    <ArticleCard className={classNames({ 'new-work-relationship': derivedState.isNew })}>
                        <Loader loading={showLoader} fill={true} />
                        <Header title={getWorkRelationshipTypeAndGroupName()}>
                            {!featureFlags.isAnyHRMSEnabled && <div className="actions">
                                {!derivedState.isEditing
                                    ? derivedState.canEditWorkRelationships && <SuperActionMenu
                                        label="Edit"
                                        ssIconAfter="angle-down"
                                        disabled={derivedState.editingDisabled}
                                        data-test="work-relationship-actions">
                                        <SuperActionMenuItem
                                            label="Edit"
                                            onClick={() => setEditing(workRelationship.id)}
                                            data-test="work-relationship-edit" />
                                        {derivedState.canDeleteWorkRelationships && <SuperActionMenuItem
                                            label="Remove Work Relationship"
                                            onClick={removeWorkRelationship}
                                            style={{ color: '#bf2e3a' }}
                                            data-test="work-relationship-remove" />}
                                    </SuperActionMenu>
                                    : <React.Fragment>
                                        {(!derivedState.isNew && derivedState.canDeleteWorkRelationships) && <SuperButton
                                            label="Remove"
                                            className="super-button-container delete"
                                            type="button"
                                            styleType="flat"
                                            ssIconBefore="trash-o"
                                            onClick={removeWorkRelationship}
                                            disabled={isProcessing}
                                            data-test="work-relationship-remove" />}
                                        <SuperButton
                                            label="Cancel"
                                            type="button"
                                            onClick={handleCancel}
                                            disabled={isProcessing}
                                            data-test="work-relationship-cancel" />
                                        <SuperButton
                                            label="Save"
                                            type="submit"
                                            styleType="pri"
                                            ssIconBefore={isProcessing ? 'spinner' : null}
                                            disabled={isProcessing || !isValid}
                                            data-test="work-relationship-save" />
                                    </React.Fragment>
                                }
                            </div>}
                        </Header>
                        <MetaDataGrid.Container>
                            <MetaDataGrid.List>
                                {derivedState.isNew
                                    && <MetaDataGrid.Item data-test="work-relationship-type">
                                        <SelectList
                                            name="workRelationshipTypeName"
                                            label="Work Relationship Type"
                                            items={workRelationshipType.enums}
                                            databindName="name"
                                            databindValue="name"
                                            required={true}
                                            customRules={validateSelectedRelationshipType()}
                                            editMode={derivedState.isEditing}
                                        />
                                    </MetaDataGrid.Item>
                                }
                                {derivedState.isEditing && derivedState.canViewStaffType
                                    && <MetaDataGrid.Item data-test="work-relationship-staff-type">
                                        <SelectFilterList
                                            name="employmentGroupId"
                                            label="Staff Type"
                                            items={employmentGroups.filter((g) => g._isActive)}
                                            databindName="name"
                                            databindValue="value"
                                            required={false}
                                            disabled={isProcessing}
                                            includeNoneOption={true}
                                            editMode={derivedState.isEditing && derivedState.canEditStaffType}
                                        />
                                    </MetaDataGrid.Item>
                                }
                                <MetaDataGrid.Item data-test="work-relationship-approved-date">
                                    <DateField
                                        label="Board Approved"
                                        name="boardApprovedDate"
                                        required={false}
                                        disabled={isProcessing}
                                        editMode={derivedState.isEditing}
                                    />
                                </MetaDataGrid.Item>
                                <MetaDataGrid.Item data-test="work-relationship-accepted-date">
                                    <DateField
                                        label="Offer Accepted"
                                        name="offerAcceptedDate"
                                        required={false}
                                        disabled={isProcessing}
                                        editMode={derivedState.isEditing}
                                    />
                                </MetaDataGrid.Item>
                                <MetaDataGrid.Item data-test="work-relationship-start-date">
                                    <DateField
                                        label="Start Date"
                                        name="startDate"
                                        required={false}
                                        disabled={isProcessing}
                                        editMode={derivedState.isEditing}
                                    />
                                </MetaDataGrid.Item>
                            </MetaDataGrid.List>
                        </MetaDataGrid.Container>
                    </ArticleCard>
                </form>
            </FormProvider>
        </section >
    );
};

WorkRelationship.propTypes = {
    workRelationship: PropTypes.object.isRequired
};

export default WorkRelationship;
