import React, { useEffect, useState, useReducer } from 'react';
import PropTypes from 'prop-types';
import {
    Loader,
    angularHelper,
    dateHelper,
    dateByOrgHelper
} from 'fc-ui-shared-components';
import { AssignmentsTabContext, assignmentTabReducer } from './context';
import AssignmentContainer from './components/assignment/container';
import SupplementalContainer from './components/supplemental/container';
import { getEmployeeAssignments } from '../../api/assignment';
import { getByExternalId } from '../../api/list';
import { setEditMode, setAddMode } from '../../actions/assignmentTab';
import './index.styl';

const AssignmentTabContent = (props) => {
    const { $injector, $stateParams, $scope } = props;
    const fields = 'hireDate,retirementSystem,seniorityDate,stateEducatorId,tenureDate';
    const resolves = [];
    const ssEmployeeApi = $injector.get('ssEmployeeApi');
    const ssJobTypeSvc = $injector.get('ssJobTypeSvc');
    const ssLocationSvc = $injector.get('ssLocationSvc');
    const ssDepartmentSvc = $injector.get('ssDepartmentSvc');
    const ssPermissionsSvc = $injector.get('ssPermissionsSvc');
    const ssOrganizationPreferencesApi = $injector.get('ssOrganizationPreferencesApi');
    const empVm = angularHelper.findInScope('employeeVm', $scope);
    const [state, dispatch] = useReducer(assignmentTabReducer, { editing: null, adding: null });
    const [isLoading, setLoading] = useState(true);
    const [positions, setPositions] = useState([]);
    const [supplementals, setSupplementals] = useState([]);
    const [supplementalTypes, setSupplementalTypes] = useState([]);
    const [positionTypes, setPositionTypes] = useState([]);
    const [jobTypes, setJobTypes] = useState([]);
    const [locations, setLocations] = useState([]);
    const [departments, setDepartments] = useState([]);
    const [contractGroups, setcontractGroups] = useState([]);
    const [employmentGroups, setEmploymentGroups] = useState([]);
    const [employeePermissions, setPermissions] = useState({});
    const [organizationPreferences, setOrganizationPreferences] = useState({});
    const isPermitted = (permissionGroup, property) => ssPermissionsSvc.isPermittedAction(permissionGroup, property);

    useEffect(() => {
        let employeeResponse;
        ssEmployeeApi.get({ id: $stateParams.employeeId, fields, include: 'statusHistory,supervisors' }).then((res) => {
            employeeResponse = res;
            setPermissions(employeeResponse.$permitted);
            return getEmployeeAssignments($injector, employeeResponse);
        }).then((res) => {
            setPositions(res);
            if (employeeResponse.$permitted.props.employeeSupplementals.view && isPermitted('employeeSupplementals', 'view')) {
                return ssEmployeeApi.getEmployeeSupplemental({ id: $stateParams.employeeId });
            }
            return Promise.resolve(null);
        }).then((res) => {
            if (res) {
                setSupplementals(res);
                resolves.push((async () => ssEmployeeApi.querySupplemental({ limit: '9999', 'sort[name]': 'asc' }))());
            } else {
                setSupplementals([]);
                resolves.push(Promise.resolve([]));
            }
            resolves.push(ssEmployeeApi.queryContractGroups());
            resolves.push((async () => ssLocationSvc.getAll().sort((a, b) => a._name.localeCompare(b._name)))());
            resolves.push((async () => ssJobTypeSvc.getAll().sort((a, b) => a._name.localeCompare(b._name)))());
            resolves.push((async () => ssDepartmentSvc.getAll().sort((a, b) => a._name.localeCompare(b._name)))());
            resolves.push(getByExternalId($injector, { listExternalId: 'position.types' }));
            resolves.push(getByExternalId($injector, { listExternalId: 'jobtype.employmentgroups' }));
            resolves.push((async () => ssOrganizationPreferencesApi.get())());
            return Promise.all(resolves);
        })
            .then((res) => {
                const [
                    supplementalTypeData,
                    contractGroupData,
                    locationData,
                    jobTypeData,
                    departmentData,
                    positionTypeData,
                    employmentGroupData,
                    orgPreferences
                ] = res;

                const mappedContractGroups = contractGroupData.map((d) => ({ _name: d._name, value: d.id }));
                if (mappedContractGroups && mappedContractGroups.length) {
                    setcontractGroups(mappedContractGroups);
                }

                setSupplementalTypes(supplementalTypeData);
                setLocations(locationData);
                setJobTypes(jobTypeData);
                setDepartments(departmentData);
                setPositionTypes(positionTypeData);
                setEmploymentGroups(employmentGroupData);
                setOrganizationPreferences(orgPreferences);
                setLoading(false);
            });
    }, []);

    const refreshPositions = async () => {
        const employee = await ssEmployeeApi.get({ id: $stateParams.employeeId, fields, include: 'statusHistory,supervisors' });
        const positionData = await getEmployeeAssignments($injector, employee);
        setPositions(positionData);
    };

    const getPrimaryAssignments = () => {
        const primaryPositions = positions.filter((p) => p.assignment && p.assignment._isPrimary);

        // sort by _startDate, _createdUtc, _startDateUtc
        primaryPositions.sort((a, b) => new Date(b._startDate) - new Date(a._startDate));
        primaryPositions.sort((a, b) => new Date(b.assignment._createdUtc) - new Date(a.assignment._createdUtc));
        primaryPositions.sort((a, b) => new Date(b.assignment._startDateUtc) - new Date(a.assignment._startDateUtc));

        return primaryPositions;
    };

    const getAdditionalAssignments = () => {
        const additionalPositions = positions.filter((p) => p.assignment && !p.assignment._isPrimary);

        // sort by _startDate, _createdUtc, _startDateUtc, _name
        additionalPositions.sort((a, b) => new Date(b._startDate) - new Date(a._startDate));
        additionalPositions.sort((a, b) => new Date(b.assignment._createdUtc) - new Date(a.assignment._createdUtc));
        additionalPositions.sort((a, b) => new Date(b.assignment._startDateUtc) - new Date(a.assignment._startDateUtc));
        additionalPositions.sort((a, b) => a._name.localeCompare(b._name));

        return additionalPositions;
    };

    const getValidMinMaxAssignmentDates = (id, isNew) => {
        const dates = { start: { min: null, max: null }, end: { min: null, max: null } };
        const primaryAssignments = getPrimaryAssignments();
        const sorted = primaryAssignments.sort((a, b) => new Date(b._endDate).getTime() - new Date(a._endDate).getTime());

        const isNewPrimary = isNew && state.adding === 'primary';
        if (isNewPrimary) {
            if (sorted.length > 0) {
                const newPrimaryMin = dateHelper.isForeverFuture(sorted[0]._endDate)
                    ? dateByOrgHelper.utcIsoToRelativeMoment(sorted[0]._startDate, empVm.org).startOf('day').add(1, 'days').format('MM/DD/YYYY')
                    : dateByOrgHelper.utcIsoToRelativeMoment(sorted[0]._endDate, empVm.org).startOf('day').add(1, 'days').format('MM/DD/YYYY');

                return {
                    start: {
                        min: newPrimaryMin,
                        max: null
                    },
                    end: {
                        min: newPrimaryMin,
                        max: null
                    }
                };
            }

            return dates;
        }

        const thisIndex = sorted.findIndex((s) => s.assignment.id === id);
        if (thisIndex < 0) return dates; // not a primary assignment

        const hasAssignmentBefore = thisIndex !== sorted.length - 1;
        const hasAssignmentAfter = thisIndex !== 0;
        let startDateMin; let startDateMax; let
            endDateMax = null;
        if (hasAssignmentBefore) {
            startDateMin = dateHelper.isForeverFuture(sorted[thisIndex + 1]._endDate)
                ? dateByOrgHelper.utcIsoToRelativeMoment(sorted[thisIndex + 1]._startDate, empVm.org).startOf('day').add(1, 'days').format('MM/DD/YYYY')
                : dateByOrgHelper.utcIsoToRelativeMoment(sorted[thisIndex + 1]._endDate, empVm.org).startOf('day').add(1, 'days').format('MM/DD/YYYY');
        }
        if (hasAssignmentAfter) {
            startDateMax = dateByOrgHelper.utcIsoToRelativeMoment(sorted[thisIndex - 1]._startDate, empVm.org).startOf('day').add(-1, 'days').format('MM/DD/YYYY');
            endDateMax = dateByOrgHelper.utcIsoToRelativeMoment(sorted[thisIndex - 1]._startDate, empVm.org).startOf('day').add(-1, 'days').format('MM/DD/YYYY');
        }

        return {
            start: {
                min: startDateMin,
                max: startDateMax
            },
            end: {
                min: startDateMin,
                max: endDateMax
            }
        };
    };

    const context = {
        state,
        dispatch,
        $injector,
        $stateParams,
        $scope,
        organization: empVm.org,
        locations,
        jobTypes,
        positionTypes,
        departments,
        supplementalTypes,
        contractGroups,
        employmentGroups,
        organizationPreferences,
        setEditMode: (mode) => {
            dispatch(setEditMode(mode));
        },
        setAddMode: (mode) => {
            dispatch(setAddMode(mode));
        },
        supplementals,
        setSupplementals,
        positions,
        setPositions,
        refreshPositions,
        employeePermissions,
        getValidMinMaxAssignmentDates,
        isPermitted
    };

    const { featureFlags } = angularHelper.findInScope('routeVm', $scope);

    return (
        <div className='assignments-tab-content'>
            <AssignmentsTabContext.Provider value={context}>
                <h3>Frontline Central Assignments</h3>
                {featureFlags.global_fc_shared_employee && <p>The following details are for Frontline Central only.</p>}
                <Loader loading={isLoading} fill={true}/>
                {!isLoading && <React.Fragment>
                    {employeePermissions.props.assignments.view && (
                        <>
                            <AssignmentContainer
                                title="Primary Assignment"
                                addType="primary"
                                positions={getPrimaryAssignments()}
                            />
                            <AssignmentContainer
                                title="Additional Assignments"
                                addType="additional"
                                positions={getAdditionalAssignments()}
                            />
                        </>
                    )}
                    {employeePermissions.props.employeeSupplementals.view
                        && <SupplementalContainer supplementals={supplementals} />
                    }
                </React.Fragment>}
            </AssignmentsTabContext.Provider>
        </div>
    );
};

AssignmentTabContent.propTypes = {
    $injector: PropTypes.object.isRequired,
    $stateParams: PropTypes.shape({
        employeeId: PropTypes.string.isRequired
    }),
    $scope: PropTypes.object.isRequired
};

export default AssignmentTabContent;
