import React, { useContext } from 'react';
import PropTypes from 'prop-types';
import { useForm, FormProvider } from 'react-hook-form';
import classNames from 'classnames';
import moment from 'moment';
import {
    ArticleCard,
    Header,
    MetaDataGrid,
    SuperActionMenu,
    SuperActionMenuItem,
    SuperButton,
    Loader,
    DateRangeWithStatus,
    SelectFilterList,
    TextField,
    currencyHelper,
    dateByOrgHelper,
    validationHelper
} from 'fc-ui-shared-components';
import { AssignmentsTabContext } from '../../context';
import { SupplementalContext } from './context';
import { useSupplementalForm } from '../../hooks/useSupplementalForm';

const Supplemental = ({ supplemental }) => {
    const creationInProgress = supplemental.id === null;

    const {
        state: tabState,
        contractGroups,
        supplementalTypes,
        organization,
        setEditMode,
        isPermitted
    } = useContext(AssignmentsTabContext);

    const selectedContractGroup = contractGroups.find((contractGroup) => supplemental._contractGroupId === contractGroup.value);
    const supplementalType = supplementalTypes.find((s) => supplemental._supplementalId === s.id);
    const supplementalName = creationInProgress ? 'Add Supplemental' : supplementalType._name;
    const supplementalForm = useSupplementalForm(supplemental, supplementalType, selectedContractGroup);

    const methods = useForm();

    const context = {
        isEditing: tabState.editing === supplemental.id || creationInProgress,
        editingDisabled: tabState.editing !== null,
    };

    const handleSave = () => (creationInProgress ? supplementalForm.create() : supplementalForm.save());

    const handleCancel = () => {
        methods.clearErrors();
        return creationInProgress ? supplementalForm.cancelCreate() : supplementalForm.cancel();
    };

    const getEndDateMin = (date) => {
        if (date) {
            return dateByOrgHelper.startOfDayRelativeTo(moment(date).startOf('day').add(1, 'days'), organization);
        }
        return null;
    };

    return (
        <SupplementalContext.Provider value={context}>
            <FormProvider {...methods}>
                <form onSubmit={methods.handleSubmit((data) => handleSave(data))} noValidate>
                    <ArticleCard className={classNames('employee-record-card supplementals-group', { 'creation-in-progress': creationInProgress, 'recently-updated': supplemental.recentlyUpdated })} data-test={`supplemental-${creationInProgress ? 'new' : supplemental.id}`}>
                        <Loader loading={supplementalForm.showLoader} fill={true} />
                        <Header title={supplementalName}>
                            <DateRangeWithStatus
                                startDate={supplementalForm.fields.startDateUtc}
                                endDate={supplementalForm.fields.endDateUtc}
                                startDateName={`supplemental-startdate-${supplemental.id || 'new'}`}
                                endDateName={`supplemental-enddate-${supplemental.id || 'new'}`}
                                startDateLabel="Start Date"
                                endDateLabel="End Date"
                                endDateMin={getEndDateMin(supplementalForm.fields.startDateUtc)}
                                startDateRequired={true}
                                endDateRequired={false}
                                endDateRules={{ validate: (endDate) => endDate && validationHelper.validateEndDateIsAfterStartDate(supplementalForm.fields.startDateUtc, dateByOrgHelper.startOfDayRelativeTo(moment(endDate), organization)) }}
                                isEditing={context.isEditing}
                                hideTag={context.isEditing || creationInProgress}
                                organization={organization}
                                handleStartDateUpdate={(val) => supplementalForm.setField('startDateUtc', val)}
                                handleEndDateUpdate={(val) => supplementalForm.setField('endDateUtc', val)}
                                disabled={supplementalForm.isProcessing} />
                            <div className="actions">
                                {!context.isEditing
                                    ? isPermitted('employeeSupplementals', 'edit') && <SuperActionMenu label="Edit" type="button" ssIconAfter="angle-down" disabled={context.editingDisabled}>
                                        <SuperActionMenuItem label="Edit" onClick={() => setEditMode(supplemental.id)} ></SuperActionMenuItem>
                                        {isPermitted('employeeSupplementals', 'softDelete') && <SuperActionMenuItem label="Delete Supplemental" onClick={supplementalForm.deleteSupplemental} style={{ color: '#bf2e3a' }}></SuperActionMenuItem>}
                                    </SuperActionMenu>
                                    : <span data-test="supplemental-cancel-save">
                                        {(supplemental.id && isPermitted('employeeSupplementals', 'softDelete')) && <SuperButton label="Delete" type="button" className="super-button-container delete" styleType="flat" ssIcon="trash-o" onClick={supplementalForm.deleteSupplemental} disabled={supplementalForm.isProcessing} />}
                                        <SuperButton label="Cancel" type="button" onClick={handleCancel} disabled={supplementalForm.isProcessing} />
                                        <SuperButton label="Save" type="submit" styleType="pri" ssIconBefore={supplementalForm.isProcessing ? 'spinner' : null} disabled={supplementalForm.isProcessing} />
                                    </span>
                                }
                            </div>
                        </Header>
                        <MetaDataGrid.Container>
                            <MetaDataGrid.List>
                                <MetaDataGrid.Item data-test="supplemental-name">
                                    <SelectFilterList
                                        name="name"
                                        label="Supplemental Name"
                                        value={supplementalForm.fields.supplementalId}
                                        onChange={(e) => supplementalForm.setField('supplementalId', e.target.value)}
                                        items={supplementalTypes}
                                        databindValue="id"
                                        editMode={context.isEditing}
                                        disabled={supplementalForm.isProcessing}
                                        required={true} />
                                </MetaDataGrid.Item>
                                <MetaDataGrid.Item data-test="supplemental-amount">
                                    <TextField
                                        name="amount"
                                        label="Total Amount"
                                        value={supplementalForm.fields.totalPayAmount}
                                        onChange={(e) => supplementalForm.setField('totalPayAmount', e.target.value)}
                                        editMode={context.isEditing}
                                        displayFormatter={currencyHelper.currency}
                                        type="number"
                                        required={true}
                                        customRules={validationHelper.validateNonNegativeValue('Total Amount')}
                                        disabled={supplementalForm.isProcessing} />
                                </MetaDataGrid.Item>
                                <MetaDataGrid.Item data-test="supplemental-installments">
                                    <TextField
                                        name="installments"
                                        label="# of Installments"
                                        value={supplementalForm.fields.totalInstallments}
                                        onChange={(e) => supplementalForm.setField('totalInstallments', e.target.value)}
                                        editMode={context.isEditing}
                                        type="number"
                                        required={true}
                                        customRules={validationHelper.validateNonNegativeValue('# of Installments')}
                                        disabled={supplementalForm.isProcessing} />
                                </MetaDataGrid.Item>
                                <MetaDataGrid.Item data-test="supplemental-contract-group">
                                    <SelectFilterList
                                        name="contractGroup"
                                        label="Contract Group"
                                        items={contractGroups}
                                        value={supplementalForm.fields.contractGroupId}
                                        editMode={context.isEditing}
                                        onChange={(e) => supplementalForm.setField('contractGroupId', e.target.value)}
                                        disabled={supplementalForm.isProcessing} />
                                </MetaDataGrid.Item>
                                <MetaDataGrid.Item data-test="supplemental-external-id">
                                    <TextField
                                        name="externalId"
                                        label="Ext. ID"
                                        value={supplementalForm.fields.externalId}
                                        onChange={(e) => supplementalForm.setField('externalId', e.target.value)}
                                        editMode={context.isEditing}
                                        required={tabState.editing === supplemental.id}
                                        customRules={tabState.editing === supplemental.id ? validationHelper.validateNotOnlyWhitespace('Ext. ID') : {}}
                                        disabled={supplementalForm.isProcessing} />
                                </MetaDataGrid.Item>
                            </MetaDataGrid.List>
                        </MetaDataGrid.Container>
                    </ArticleCard>
                </form>
            </FormProvider>
        </SupplementalContext.Provider>
    );
};

Supplemental.propTypes = {
    supplemental: PropTypes.object.isRequired,
};

export default Supplemental;
