import React, { useContext, useEffect, useState } from 'react';
import PropTypes from 'prop-types';
import classNames from 'classnames';
import moment from 'moment';
import { FormProvider, useForm } from 'react-hook-form';
import {
    ArticleCard,
    Header,
    MetaDataGrid,
    SuperActionMenu,
    SuperActionMenuItem,
    SuperButton,
    SuperInput,
    Loader,
    DateField,
    dateHelper,
    dateByOrgHelper,
    validationHelper,
    logger
} from 'fc-ui-shared-components';
import { EmploymentTabContext } from '../../../context';
import { GeneralTabContext } from '../../context';
import SupervisorTypeahead from './search/supervisorTypeahead';

const Supervisor = ({ supervisor }) => {
    const [isProcessing, setProcessing] = useState(false);
    const [showLoader, setShowLoader] = useState(false);
    const {
        id,
        isCurrent,
        isFuture,
        isPast,
        _employeeId,
        _firstName,
        _lastName,
        _from,
        _to
    } = supervisor;

    const {
        employee, org, $injector, permissions, refreshEmployee
    } = useContext(EmploymentTabContext);
    const {
        editing, adding, setAdding, setEditing, cancelNewSupervisor
    } = useContext(GeneralTabContext);

    const ssToastHlp = $injector.get('ssToastHlp');

    const derivedState = {
        isNew: id === null,
        isEditing: editing === id || id === null,
        editingDisabled: editing !== null || adding,
        canView: permissions.props.supervisor.view,
        canEdit: permissions.props.supervisor.edit
    };

    const methods = useForm({
        defaultValues: {
            employeeId: _employeeId,
            from: _from ? dateByOrgHelper.utcIsoToRelativeMoment(_from, org).format('MM/DD/YYYY') : '',
            to: (!_to || dateHelper.isForeverFuture(_to)) ? '' : dateByOrgHelper.utcIsoToRelativeMoment(_to, org).format('MM/DD/YYYY')
        }
    });

    useEffect(() => {
        methods.reset({
            employeeId: _employeeId,
            from: _from ? dateByOrgHelper.utcIsoToRelativeMoment(_from, org).format('MM/DD/YYYY') : '',
            to: (!_to || dateHelper.isForeverFuture(_to)) ? '' : dateByOrgHelper.utcIsoToRelativeMoment(_to, org).format('MM/DD/YYYY')
        });
    }, [_employeeId, _from, _to]);

    const getHeaderTitle = () => {
        if (derivedState.isNew) return 'New Supervisor';
        if (isFuture) return 'Future Supervisor';
        if (isCurrent) return 'Current Supervisor';
        if (isPast) return 'Past Supervisor';
        return '';
    };

    const startDateValidation = () => ({
        validate: {
            supervisorStartDateValid: (val) => {
                const { to } = methods.getValues();
                const endDate = to || dateHelper.foreverFuture;
                return validationHelper.validateStartDateIsBeforeEndDate(val, endDate);
            }
        }
    });

    const endDateValidation = () => ({
        validate: {
            supervisorEndDateValid: (val) => {
                const { from } = methods.getValues();
                const endDate = val || dateHelper.foreverFuture;
                return validationHelper.validateEndDateIsAfterStartDate(from, endDate);
            }
        }
    });

    // eslint-disable-next-line no-promise-executor-return
    const delay = (ms) => new Promise((resolve) => setTimeout(resolve, ms));

    const normalizeDate = (date) => (dateHelper.isDate(date) ? date.toISOString() : date);

    const createNewSupervisor = async () => {
        setProcessing(true);
        const fields = methods.getValues();
        const startDate = normalizeDate(fields.from);
        const endDate = normalizeDate(fields.to);

        const newSupervisor = {
            _employeeId: fields.employeeId,
            _from: startDate && dateByOrgHelper.startOfDayRelativeTo(moment(startDate).startOf('day'), org),
            _to: (!endDate || dateHelper.isForeverFuture(endDate))
                ? dateHelper.foreverFuture
                : dateByOrgHelper.endOfDayRelativeTo(moment(endDate).endOf('day'), org)
        };
        try {
            await employee.$refresh();
            const lastestSupervisor = employee._supervisors.sort((a, b) => new Date(b._from) - new Date(a._from))[0];
            if (lastestSupervisor) {
                lastestSupervisor._to = dateHelper.isForeverFuture(lastestSupervisor._to)
                    ? dateByOrgHelper.utcIsoToRelativeMoment(newSupervisor._from, org)
                        .clone().add(-1, 'days').endOf('day')
                        .toISOString()
                    : lastestSupervisor._to;
            }
            employee._supervisors.push(newSupervisor);
            await employee._supervisors.$save({ opOrder: ['remove', 'modify', 'add'] });
            await delay(1000);
            await refreshEmployee();
            ssToastHlp.success('Supervisor has been created');
        } catch (e) {
            ssToastHlp.error(e, 'Save failed');
        } finally {
            setProcessing(false);
            setEditing(null);
            setAdding(false);
        }
    };

    const saveSupervisor = async () => {
        setProcessing(true);
        const fields = methods.getValues();
        try {
            await employee.$refresh();
            const startDate = normalizeDate(fields.from);
            const endDate = normalizeDate(fields.to);
            const patchSupervisor = employee._supervisors.find((s) => s.id === id);
            patchSupervisor._from = startDate && dateByOrgHelper.startOfDayRelativeTo(moment(startDate).startOf('day'), org);
            patchSupervisor._to = endDate
                ? dateByOrgHelper.endOfDayRelativeTo(moment(endDate).endOf('day'), org)
                : dateHelper.foreverFuture;
            await employee._supervisors.$save({ opOrder: ['remove', 'modify', 'add'] });
            await delay(1000);
            await refreshEmployee();
            ssToastHlp.success('Supervisor has been saved');
        } catch (e) {
            refreshEmployee();
        } finally {
            setProcessing(false);
            setEditing(null);
        }
    };

    const removeSupervisor = async () => {
        setEditing('removing');
        let confirm = false;

        try {
            const modalSvc = $injector.get('ssModalSvc');
            confirm = await modalSvc.showDestructive({
                title: 'Are you sure?',
                content: 'Are you sure you want to delete this supervisor?',
                primaryText: 'Delete Supervisor',
                secondaryText: 'Cancel'
            });
        } catch {
            // remove supervisor cancelled
            logger.log('cancelled');
            setEditing(null);
        }
        if (confirm) {
            setShowLoader(true);
            try {
                await employee.$refresh();
                await supervisor.$remove();
                await delay(1000);
                await refreshEmployee();
                ssToastHlp.success('Supervisor has been removed');
            } catch (e) {
                refreshEmployee();
            } finally {
                setEditing(null);
                setShowLoader(false);
            }
        }
    };

    const handleCancel = () => {
        methods.clearErrors();
        methods.reset();
        if (derivedState.isNew) { cancelNewSupervisor(); }
        setEditing(null);
    };

    return (
        <section className='supervisor'>
            <FormProvider {...methods} >
                <form onSubmit={methods.handleSubmit(derivedState.isNew ? createNewSupervisor : saveSupervisor)} noValidate>
                    <ArticleCard className={classNames({ 'new-supervisor': derivedState.isNew })}>
                        <Loader loading={showLoader} fill={true} />
                        <Header title={getHeaderTitle()}>
                            <div className="actions">
                                {!derivedState.isEditing
                                    ? derivedState.canEdit && <SuperActionMenu
                                        label="Edit"
                                        ssIconAfter="angle-down"
                                        disabled={derivedState.editingDisabled}
                                        data-test="supervisor-actions">
                                        <SuperActionMenuItem
                                            label="Edit"
                                            onClick={() => setEditing(supervisor.id)}
                                            data-test="supervisor-edit" />
                                        <SuperActionMenuItem
                                            label="Remove Supervisor"
                                            onClick={removeSupervisor}
                                            style={{ color: '#bf2e3a' }}
                                            data-test="supervisor-remove" />
                                    </SuperActionMenu>
                                    : <React.Fragment>
                                        {(!derivedState.isNew && derivedState.canEdit) && <SuperButton
                                            label="Remove"
                                            className="super-button-container delete"
                                            type="button"
                                            styleType="flat"
                                            ssIconBefore="trash-o"
                                            onClick={removeSupervisor}
                                            disabled={isProcessing}
                                            data-test="supervisor-remove" />}
                                        <SuperButton
                                            label="Cancel"
                                            type="button"
                                            onClick={handleCancel}
                                            disabled={isProcessing}
                                            data-test="supervisor-cancel" />
                                        <SuperButton
                                            label="Save"
                                            type="submit"
                                            styleType="pri"
                                            ssIconBefore={isProcessing ? 'spinner' : null}
                                            disabled={isProcessing}
                                            data-test="supervisor-save" />
                                    </React.Fragment>
                                }
                            </div>
                        </Header>
                        <MetaDataGrid.Container>
                            <MetaDataGrid.List>
                                <MetaDataGrid.Item data-test="supervisor-name">
                                    {derivedState.isNew
                                        ? <>
                                            <SupervisorTypeahead
                                                name="employeeId"
                                                required={true}
                                                disabled={isProcessing}
                                                placeholder="Search staff members..." />
                                        </>
                                        : <SuperInput
                                            label="Name"
                                            name="name"
                                            required={false}
                                            disabled={isProcessing}
                                            editMode={false}
                                            value={`${_firstName} ${_lastName}`} />
                                    }
                                </MetaDataGrid.Item>
                                <MetaDataGrid.Item data-test="supervisor-from">
                                    <DateField
                                        label="Start Date"
                                        name="from"
                                        required={true}
                                        disabled={isProcessing}
                                        editMode={derivedState.isEditing}
                                        customRules={startDateValidation()}
                                    />
                                </MetaDataGrid.Item>
                                <MetaDataGrid.Item data-test="supervisor-to">
                                    <DateField
                                        label="End Date"
                                        name="to"
                                        required={false}
                                        disabled={isProcessing}
                                        editMode={derivedState.isEditing}
                                        customRules={endDateValidation()}
                                    />
                                </MetaDataGrid.Item>
                            </MetaDataGrid.List>
                        </MetaDataGrid.Container>
                    </ArticleCard>
                </form>
            </FormProvider>
        </section >
    );
};

Supervisor.propTypes = {
    supervisor: PropTypes.object.isRequired
};

export default Supervisor;
