import React, { useEffect, useState } from 'react';
import PropTypes from 'prop-types';
import {
    Loader,
    TabListNav
} from 'fc-ui-shared-components';

import GeneralTabContent from './general';
import I9TabContent from './I9';
import { EmploymentTabContext } from './context';
import './index.styl';

const EmploymentTabContent = ({ $injector, $stateParams }) => {
    const featureFlagsToResolve = {
        fc_enable_employee_module_save_interrupter: false,
        fc_work_relationships: false,
        fc_enable_tabbed_employment_content: false,
        fc_staff_service_work_relationship: false,
        fc_enable_employment_I9_tab: false,
        isDataIntegrationsEnabled: false,
        showTenureDate: false,
        isGlobalFcSharedEmployeeEnabled: false,
        isHRMSEnabled: false,
        isAnyHRMSEnabled: false
    };
    const ssOrganizationSvc = $injector.get('ssOrganizationSvc');
    const ssMultiInitiativeFeatureDetectorCCSvc = $injector.get('ssMultiInitiativeFeatureDetectorCCSvc');
    const ssEmployeeApi = $injector.get('ssEmployeeApi');
    const ssToastHlp = $injector.get('ssToastHlp');
    const [isLoading, setLoading] = useState(true);
    const [employee, setEmployee] = useState(null);
    const [employeePermissions, setEmployeePermissions] = useState(null);
    const [organization, setOrganization] = useState(null);
    const [adding, setAdding] = useState(null);
    const [editing, setEditing] = useState(null);
    const [featureFlags, setFeatureFlags] = useState(featureFlagsToResolve);

    const getEmployee = async () => {
        let includes = ['statusHistory', 'supervisors'];
        let fields = ['originalStartDate', 'hireDate', 'retirementSystem', 'seniorityDate', 'stateEducatorId', 'firstName', 'lastName', 'preferredName', 'middleName', 'i9Data'];

        if (!featureFlags.fc_staff_service_work_relationship) {
            includes = [...includes, 'workRelationships'];
        }
        if (featureFlags.showTenureDate) {
            fields = [...fields, 'tenureDate'];
        }
        if (featureFlags.fc_enable_employment_I9_tab) {
            includes = [...includes, 'i9forminstances', 'I9EmployeeData'];
        }

        return ssEmployeeApi.get({
            id: $stateParams.employeeId,
            fields: fields.join(','),
            include: includes.join(',')
        }, null, false);
    };

    const refreshEmployee = async (recentlySavedEmployee) => {
        if (recentlySavedEmployee) {
            setEmployee(recentlySavedEmployee);
            return;
        }
        const res = await getEmployee();
        setEmployee(res);
        setEmployeePermissions(res.$permitted);
    };

    useEffect(() => {
        (async () => {
            try {
                await ssMultiInitiativeFeatureDetectorCCSvc.resolveAll(featureFlagsToResolve);
                setFeatureFlags(featureFlagsToResolve);
                const orgData = await ssOrganizationSvc.getActiveOrg();
                setOrganization(orgData);
                await refreshEmployee();
            } catch (e) {
                ssToastHlp.error(e);
            } finally {
                setLoading(false);
            }
        })();
    }, []);

    const context = {
        $injector,
        $stateParams,
        featureFlags,
        employee,
        permissions: employeePermissions,
        refreshEmployee,
        org: organization,
        adding,
        editing,
        setAdding,
        setEditing
    };

    const canViewI9Tab = () => featureFlags.fc_enable_employment_I9_tab && employeePermissions.actions.canViewI9Fields;

    const canViewGeneralTab = () => {
        const { props: properties } = employeePermissions;
        return properties.hireDate.view
            || properties.originalStartDate.view
            || properties.seniorityDate.view
            || properties.tenureDate.view
            || properties.retirementSystem.view
            || properties.statusHistory.view
            || properties.supervisor.view
            || properties.staffType.view;
    };

    const showTabSet = () => featureFlags.fc_enable_tabbed_employment_content && canViewI9Tab() && canViewGeneralTab();

    return (
        <div className='employment-tab-content'>
            <EmploymentTabContext.Provider value={context}>
                {(!isLoading && showTabSet())
                    && <TabListNav links={[
                        {
                            text: 'General',
                            href: `/ec-employee/record/${$stateParams.employeeId}/employment/general`,
                            active: $stateParams.subRoute === 'general',
                        },
                        {
                            text: 'I-9 Form',
                            href: `/ec-employee/record/${$stateParams.employeeId}/employment/I9`,
                            active: $stateParams.subRoute === 'I9',
                        },
                    ]}></TabListNav>
                }
                <div className="employment-tab-content-wrapper">
                    <Loader loading={isLoading} fill={true} />
                    {!isLoading && <>
                        {($stateParams.subRoute === 'general' && canViewGeneralTab()) && <GeneralTabContent />}
                        {($stateParams.subRoute === 'I9' && canViewI9Tab()) && <I9TabContent />}
                    </>}
                </div>
            </EmploymentTabContext.Provider>
        </div>
    );
};

EmploymentTabContent.propTypes = {
    $injector: PropTypes.object.isRequired,
    $stateParams: PropTypes.shape({
        employeeId: PropTypes.string,
        subRoute: PropTypes.string
    }).isRequired,
};

export default EmploymentTabContent;
