/* eslint-disable object-curly-newline */
import React, { useContext, useState, useEffect } from 'react';
import { useForm, FormProvider } from 'react-hook-form';
import moment from 'moment';
import {
    ArticleCard,
    Header,
    MetaDataGrid,
    SuperButton,
    SelectFilterList,
    DateField,
    TextField,
    dateByOrgHelper,
    permissionsHelper,
    logger
} from 'fc-ui-shared-components';
import { EmploymentTabContext } from '../../../context';
import { GeneralTabContext } from '../../context';

import { getByExternalId, getActiveItems } from '../../../../../api/list';

const OtherDatesTenureRetirement = () => {
    const [isProcessing, setProcessing] = useState(true);
    const [editMode, setEditMode] = useState(false);
    const [retirementSystems, setRetirementSystems] = useState([]);
    const { featureFlags, org, $injector, employee, refreshEmployee } = useContext(EmploymentTabContext);
    const { editing, setEditing } = useContext(GeneralTabContext);
    const { props: propPermissions, actions: actionPermissions } = employee.$permitted;
    const { _originalStartDate, _retirementSystem, _hireDate, _seniorityDate, _tenureDate, _externalId } = employee;
    const ssToastHlp = $injector.get('ssToastHlp');
    const sectionId = 'ODTR';

    function getRetirementSystem(id) {
        const none = { name: 'None', id: -1, value: -1 };
        if (!id) return none;
        const selected = retirementSystems.find((rs) => rs.value === id);
        return selected ? { id: selected.value, name: selected.name } : none;
    }

    function setFieldDefaults() {
        return {
            originalStartDate: _originalStartDate,
            retirementSystem: _retirementSystem?.id,
            hireDate: _hireDate,
            seniorityDate: _seniorityDate,
            tenureDate: _tenureDate,
            externalId: _externalId
        };
    }

    const methods = useForm({
        defaultValues: setFieldDefaults()
    });

    useEffect(() => {
        methods.reset(setFieldDefaults());
    }, [employee]);

    useEffect(async () => {
        try {
            const res = await getByExternalId($injector, { listExternalId: 'employee.retirementsystems' });
            getActiveItems(res, employee._retirementSystem?.value);
            setRetirementSystems(res);
        } catch (err) {
            logger.log(err);
        } finally {
            setProcessing(false);
        }
    }, []);

    const handleEdit = () => {
        setEditing(sectionId);
        setEditMode(true);
    };

    const handleCancel = () => {
        methods.clearErrors();
        methods.reset();
        setEditing(null);
        setEditMode(false);
    };

    const handleSave = async () => {
        setProcessing(true);
        const formValues = methods.getValues();

        const rs = getRetirementSystem(formValues.retirementSystem);
        if (employee._retirementSystem) {
            employee._retirementSystem.id = rs.id;
            employee._retirementSystem.name = rs.name;
        } else {
            employee._retirementSystem = rs;
        }

        employee._hireDate = formValues.hireDate && dateByOrgHelper.startOfDayRelativeTo(moment(formValues.hireDate).startOf('day'), org);
        employee._originalStartDate = formValues.originalStartDate && dateByOrgHelper.startOfDayRelativeTo(moment(formValues.originalStartDate).startOf('day'), org);
        employee._seniorityDate = formValues.seniorityDate && dateByOrgHelper.startOfDayRelativeTo(moment(formValues.seniorityDate).startOf('day'), org);
        employee._tenureDate = formValues.tenureDate && dateByOrgHelper.startOfDayRelativeTo(moment(formValues.tenureDate).startOf('day'), org);
        employee._externalId = formValues.externalId && dateByOrgHelper.startOfDayRelativeTo(moment(formValues.externalId).startOf('day'), org);

        try {
            const res = await employee.$save();
            await refreshEmployee(res.data);
            ssToastHlp.success('changes saved');
        } catch (e) {
            ssToastHlp.error(e);
        } finally {
            setEditMode(false);
            setEditing(null);
            setProcessing(false);
        }
    };

    const canEdit = () => {
        const propsToCheck = [
            propPermissions.originalStartDate.edit,
            propPermissions.hireDate.edit,
            propPermissions.seniorityDate.edit,
            propPermissions.tenureDate.edit,
            propPermissions.retirementSystem.edit,
            (featureFlags.isDataIntegrationsEnabled && propPermissions.stateEducatorId.edit),
        ];
        const hasEditableFields = propsToCheck.some((p) => p);
        return !editMode && actionPermissions.edit && hasEditableFields;
    };

    return (
        <div className="record">
            <header>
                <h3>Other Dates, Tenure &amp; Retirement</h3>
            </header>
            <FormProvider {...methods}>
                <form onSubmit={methods.handleSubmit(handleSave)} noValidate>
                    <ArticleCard>
                        <Header>
                            <div className="actions">
                                {canEdit() && <SuperButton
                                    label="Edit"
                                    onClick={handleEdit}
                                    disabled={editing !== null}
                                    data-test="other-dates-edit" />}
                                {editMode && <React.Fragment>
                                    <SuperButton
                                        onClick={handleCancel}
                                        label="Cancel"
                                        disabled={isProcessing}
                                        data-test="other-dates-cancel" />
                                    <SuperButton
                                        type="submit"
                                        label="Save"
                                        styleType='pri'
                                        ssIconBefore={isProcessing ? 'spinner' : null}
                                        disabled={isProcessing}
                                        data-test="other-dates-save" />
                                </React.Fragment>}
                            </div>
                        </Header>
                        <MetaDataGrid.Container>
                            <MetaDataGrid.List>
                                { !featureFlags.isAnyHRMSEnabled && propPermissions.originalStartDate.view && <MetaDataGrid.Item data-test="original-start-date">
                                    <DateField
                                        name="originalStartDate"
                                        label="Original Start Date"
                                        required={false}
                                        editMode={editMode && propPermissions.originalStartDate.edit}
                                        disabled={isProcessing}
                                    />
                                </MetaDataGrid.Item>}
                                {propPermissions.hireDate.view && <MetaDataGrid.Item data-test="hire-date">
                                    <DateField
                                        name="hireDate"
                                        label="Hire Date"
                                        required={false}
                                        editMode={!featureFlags.isAnyHRMSEnabled && editMode && propPermissions.hireDate.edit}
                                        disabled={isProcessing}
                                    />
                                </MetaDataGrid.Item>}
                                {propPermissions.seniorityDate.view && <MetaDataGrid.Item data-test="seniority-date">
                                    <DateField
                                        name="seniorityDate"
                                        label="Seniority Date"
                                        required={false}
                                        editMode={editMode && propPermissions.seniorityDate.edit}
                                        redaction={permissionsHelper.getRedactionState(propPermissions.seniorityDate)}
                                        disabled={isProcessing}
                                    />
                                </MetaDataGrid.Item>}
                                {featureFlags.showTenureDate && propPermissions.tenureDate.view && <MetaDataGrid.Item data-test="tenure-date">
                                    <DateField
                                        name="tenureDate"
                                        label="Tenure Date"
                                        required={false}
                                        editMode={editMode && propPermissions.tenureDate.edit}
                                        redaction={permissionsHelper.getRedactionState(propPermissions.tenureDate)}
                                        disabled={isProcessing}
                                    />
                                </MetaDataGrid.Item>}
                                {retirementSystems.length && propPermissions.retirementSystem.view && <MetaDataGrid.Item data-test="retirement-system">
                                    <SelectFilterList
                                        name="retirementSystem"
                                        label="Retirement System"
                                        items={retirementSystems}
                                        editMode={editMode && propPermissions.retirementSystem.edit}
                                        disabled={isProcessing}
                                        databindName="name"
                                        databindValue="value"
                                        includeNoneOption={false}
                                    />
                                </MetaDataGrid.Item>}
                                {featureFlags.isDataIntegrationsEnabled && propPermissions.stateEducatorId.view && <MetaDataGrid.Item data-test="state-educator-id">
                                    <TextField
                                        name="externalId"
                                        label="State Educator ID"
                                        value={''}
                                        editMode={editMode && propPermissions.stateEducatorId.edit}
                                        disabled={isProcessing}
                                    />
                                </MetaDataGrid.Item>}
                            </MetaDataGrid.List>
                        </MetaDataGrid.Container>
                    </ArticleCard>
                </form>
            </FormProvider>
        </div>
    );
};

export default OtherDatesTenureRetirement;
