/* eslint-disable object-curly-newline */
import React, { useContext, useEffect, useState } from 'react';
import PropTypes from 'prop-types';
import { FormProvider, useForm } from 'react-hook-form';
import classNames from 'classnames';
import moment from 'moment';
import {
    dateHelper,
    dateByOrgHelper,
    employeeStatusEnums,
    logger,
    EffectiveDateTag,
    ArticleCard,
    DateField,
    SelectList,
    Header,
    SuperActionMenu,
    SuperActionMenuItem,
    SuperButton,
    Loader
} from 'fc-ui-shared-components';
import { getByExternalId } from '../../../../../api/list';
import './styles.styl';
import { EmploymentHistoryContext } from './context';
import { EmploymentTabContext } from '../../../context';
import { GeneralTabContext } from '../../context';

const StatusHistoryItem = ({ status }) => {
    const [isProcessing, setProcessing] = useState(false);
    const [showLoader, setShowLoader] = useState(false);
    const [editMode, setEditMode] = useState(!status.id);
    const [statusChangeReasons, setStatusChangeReasons] = useState([]);
    const [showStatusChangeReasons, setShowStatusChangeReasons] = useState(false);

    const { org, $injector, employee, refreshEmployee } = useContext(EmploymentTabContext);
    const { editing, setEditing, setAdding, adding } = useContext(GeneralTabContext);
    const { setNewStatus, employmentHistory } = useContext(EmploymentHistoryContext);
    const toastHlp = $injector.get('ssToastHlp');
    const modalSvc = $injector.get('ssModalSvc');

    const { props: propPermissions } = employee.$permitted;

    const {
        _statusId, _to, _from, _statusChangeReasonId
    } = status;

    const derivedState = {
        isNew: status.id === null,
        isEditing: editing === status.id || status.id === null,
        editingDisabled: editing !== null || adding,
        isCurrent: dateByOrgHelper.isCurrent({ fromDate: status._from, toDate: status._to, org })
    };
    const defaultFieldValues = {
        statusId: _statusId,
        statusFromDate: _from ? dateByOrgHelper.utcIsoToRelativeMoment(_from, org).format('MM/DD/YYYY') : '',
        statusToDate: (!_to || dateHelper.isForeverFuture(_to)) ? '' : dateByOrgHelper.utcIsoToRelativeMoment(_to, org).format('MM/DD/YYYY'),
        statusChangeReasonId: _statusChangeReasonId
    };

    const methods = useForm({
        defaultValues: defaultFieldValues
    });

    const { isValid } = methods.formState;

    // eslint-disable-next-line no-promise-executor-return
    const delay = (ms) => new Promise((resolve) => setTimeout(resolve, ms));

    function showStatusReasons() {
        const { statusId, statusChangeReasonId } = methods.getValues();

        if (parseInt(statusId, 10) !== employeeStatusEnums.active) {
            setShowStatusChangeReasons(editMode || !!statusChangeReasonId);
            return;
        }
        methods.setValue('statusChangeReasonId', '');
        setShowStatusChangeReasons(false);
    }

    function getMappedStatusObject() {
        const formValues = methods.getValues();
        return {
            _statusId: formValues.statusId,
            _to: !formValues.statusToDate ? dateHelper.foreverFuture : dateByOrgHelper.endOfDayRelativeTo(moment(formValues.statusToDate), org),
            _from: formValues.statusFromDate && dateByOrgHelper.startOfDayRelativeTo(moment(formValues.statusFromDate), org),
            _statusChangeReasonId: formValues.statusChangeReasonId,
        };
    }

    function getCurrentHistoryState() {
        const history = employmentHistory.slice();
        let mappedObj = getMappedStatusObject();

        if (derivedState.isNew) {
            mappedObj = {
                ...mappedObj,
                id: status.id
            };
            return [...history, mappedObj];
        }
        const idx = history.findIndex((h) => h.id === status.id);
        history[idx] = { ...history[idx], ...mappedObj };
        return history;
    }

    const saveStatus = async () => {
        setProcessing(true);
        try {
            const updatedEmp = await employee.$refresh();
            const mappedObject = getMappedStatusObject();

            if (derivedState.isNew) {
                updatedEmp._statusHistory.push(mappedObject);
            } else {
                const matchingStatus = updatedEmp._statusHistory.find((s) => s.id === status.id);
                Object.assign(matchingStatus, mappedObject);
            }
            await updatedEmp.$save();
            await updatedEmp._statusHistory.$save();
            await delay(1000);
            await refreshEmployee();
            toastHlp.success(`status has been ${derivedState.isNew ? 'added' : 'saved'}`);
        } catch (err) {
            logger.error(err);
            toastHlp.error(err, 'Save failed');
        } finally {
            setProcessing(false);
            setEditing(null);
            setEditMode(false);
            setAdding(false);
            setNewStatus([]);
        }
    };

    const removeStatus = () => {
        const confirmation = {
            title: 'Are you sure?',
            content: 'Are you sure you want to delete this status?',
            primaryText: 'Delete Status',
            secondaryText: 'Cancel'
        };

        modalSvc.showDestructive(confirmation)
            .then(async () => {
                try {
                    setShowLoader(true);
                    await employee.$refresh();
                    await status.$remove(true, true, false);
                    await delay(1000);
                    await refreshEmployee();
                    toastHlp.success('Status has been removed');
                } catch (err) {
                    toastHlp.error('', 'Remove status failed');
                    logger.error(err);
                } finally {
                    setShowLoader(false);
                    setEditing(null);
                }
            });
    };

    const handleEdit = async function () {
        const confirmation = {
            title: 'Are you sure?',
            content: 'If this employee is undergoing a status change, do not edit this current status. Rather use "Add Status Change" option.',
            primaryText: 'Continue Editing',
            secondaryText: 'Cancel'
        };

        const showModal = derivedState.isCurrent ? modalSvc.showDestructive(confirmation) : Promise.resolve();

        try {
            await showModal;
            setEditing(status.id);
            setEditMode(true);
            return true;
        } catch (e) {
            return false;
        }
    };

    const handleCancel = () => {
        methods.clearErrors();
        methods.reset(defaultFieldValues);
        if (derivedState.isNew) {
            setNewStatus([]);
            setAdding(false);
        }
        setEditing(null);
        setEditMode(false);
    };

    const handleStatusChange = () => {
        showStatusReasons();
    };

    const startDateValidation = {
        validate: {
            employmentHistoryStartDateValid: (val) => {
                const { statusToDate } = methods.getValues();
                const toDateISO = statusToDate ? dateByOrgHelper.endOfDayRelativeTo(moment(statusToDate).startOf('day'), org) : dateHelper.foreverFuture;
                const fromDateISO = val ? dateByOrgHelper.startOfDayRelativeTo(moment(val), org) : dateHelper.foreverPast;
                return fromDateISO <= toDateISO || 'Start Date must not be later than the End Date';
            },
            noOverlap: (val) => {
                const history = getCurrentHistoryState();
                const sorted = dateHelper.sortByStartDate(history, '_from');
                const thisIndex = sorted.findIndex((s) => s.id === status.id);
                if (thisIndex <= 0) return true;
                const fromDateISO = val ? dateByOrgHelper.startOfDayRelativeTo(moment(val), org) : dateHelper.foreverPast;
                const previousToDateISO = dateByOrgHelper.endOfDayRelativeTo(moment(sorted[thisIndex - 1]._to), org) || dateHelper.foreverFuture;
                return moment(fromDateISO).isAfter(moment(previousToDateISO)) || 'Start Date cannot overlap with another status';
            }
        }
    };

    const endDateValidation = {
        validate: {
            employmentHistoryEndDateValid: (val) => {
                const { statusFromDate } = methods.getValues();
                const fromDateISO = val ? dateByOrgHelper.startOfDayRelativeTo(moment(statusFromDate), org) : dateHelper.foreverPast;
                const toDateISO = val ? dateByOrgHelper.endOfDayRelativeTo(moment(val).startOf('day'), org) : dateHelper.foreverFuture;
                return fromDateISO <= toDateISO || 'End Date must not be earlier than the Start Date';
            },
            noOverlap: (val) => {
                const toDateISO = val ? dateByOrgHelper.endOfDayRelativeTo(moment(val).startOf('day'), org) : dateHelper.foreverFuture;
                const history = getCurrentHistoryState();
                const sorted = dateHelper.sortByEndDate(history, '_to');
                const thisIndex = sorted.findIndex((s) => s.id === status.id);
                if (thisIndex === sorted.length - 1) return true;
                const nextFromDateISO = dateByOrgHelper.startOfDayRelativeTo(moment(sorted[thisIndex + 1]._from), org);
                return moment(toDateISO).isBefore(moment(nextFromDateISO)) || 'End Date cannot overlap with another status';
            }
        }
    };

    useEffect(() => {
        methods.reset(defaultFieldValues);
    }, [_statusId, _to, _from, _statusChangeReasonId]);

    useEffect(() => {
        showStatusReasons();
    }, [editMode]);

    useEffect(async () => {
        setProcessing(true);
        try {
            const res = await getByExternalId($injector, { listExternalId: 'employee.statuschangereasons' });
            setStatusChangeReasons(res);
        } catch (e) {
            logger.error(e);
        } finally {
            setProcessing(false);
        }
    }, []);

    return (
        <FormProvider {...methods}>
            <form onSubmit={methods.handleSubmit(saveStatus)} noValidate >
                <ArticleCard className={classNames('employment-history-item', { 'new-employment-status': derivedState.isNew })}>
                    <Loader loading={showLoader} fill={true} />
                    <Header title={''}>
                        {!derivedState.isNew && <EffectiveDateTag config={status.statusTagConfig} />}
                        <div className="actions">
                            {/* { show button for current status } */}
                            {!editMode && propPermissions.statusHistory.edit && derivedState.isCurrent && <SuperButton
                                label="Edit"
                                type="button"
                                onClick={handleEdit}
                                disabled={derivedState.editingDisabled}
                                data-test="employment-status-edit" />}
                            {/* { show action menu for statuses NOT in effect } */}
                            {(!editMode && propPermissions.statusHistory.edit && !derivedState.isCurrent)
                                && <SuperActionMenu
                                    label="Edit"
                                    ssIconAfter="angle-down"
                                    disabled={isProcessing || derivedState.editingDisabled}
                                    data-test="employment-status-actions"
                                    ssIconBefore={isProcessing ? 'spinner' : null}>
                                    <SuperActionMenuItem
                                        label="Edit"
                                        onClick={handleEdit}
                                        data-test="employment-status-edit" />
                                    {!derivedState.isCurrent
                                    && <SuperActionMenuItem
                                        label="Remove Status"
                                        onClick={removeStatus}
                                        style={{ color: '#bf2e3a' }}
                                        data-test="employment-status-remove" />}
                                </SuperActionMenu>}
                            {/* { EDIT mode actions } */}
                            {editMode && <React.Fragment>
                                {/* { do not display 'remove' for current status} */}
                                {(!derivedState.isNew && !derivedState.isCurrent)
                                && <SuperButton
                                    label="Remove"
                                    className="super-button-container delete"
                                    type="button"
                                    styleType="flat"
                                    ssIconBefore="trash-o"
                                    onClick={removeStatus}
                                    disabled={isProcessing}
                                    data-test="employment-status-remove" />}
                                <SuperButton
                                    label="Cancel"
                                    type="button"
                                    onClick={handleCancel}
                                    disabled={isProcessing}
                                    data-test="employment-status-cancel" />
                                <SuperButton
                                    label="Save"
                                    type="submit"
                                    styleType="pri"
                                    ssIconBefore={isProcessing ? 'spinner' : null}
                                    disabled={isProcessing || !isValid}
                                    data-test="employment-status-save" />
                            </React.Fragment>}
                        </div>
                    </Header>
                    <div className="employment-history-field-wrapper">
                        <SelectList
                            name="statusId"
                            items={employeeStatusEnums.enums}
                            databindName="name"
                            databindValue="value"
                            editMode={editMode}
                            disabled={isProcessing}
                            onChange={handleStatusChange}
                            data-test="status-level"
                        />
                        <p className="connecting-text">{status.category === 'upcoming' ? 'starting on' : 'from'}</p>
                        <DateField
                            name="statusFromDate"
                            required={true}
                            editMode={editMode}
                            disabled={isProcessing}
                            data-test="status-from-date"
                            customRules={startDateValidation}
                        />
                        {(editMode || (status._to !== null && !dateHelper.isForeverFuture(status._to)))
                        && <>
                            <p className="connecting-text">to</p>
                            <DateField
                                name="statusToDate"
                                required={false}
                                editMode={editMode}
                                disabled={isProcessing}
                                data-test="status-to-date"
                                customRules={endDateValidation}
                            />
                        </>}
                        {showStatusChangeReasons
                        && <>
                            <p className="connecting-text">Reason:&nbsp;</p>
                            <SelectList
                                name="statusChangeReasonId"
                                items={statusChangeReasons}
                                databindName="name"
                                databindValue="value"
                                editMode={editMode}
                                disabled={isProcessing || !statusChangeReasons.length}
                                data-test="status-change-reason"
                            />
                        </>}
                    </div>
                </ArticleCard>
            </form>
        </FormProvider>
    );
};

StatusHistoryItem.propTypes = {
    status: PropTypes.object.isRequired
};

export default StatusHistoryItem;
