import React, { useContext, useEffect, useState } from 'react';
import { AddableContainer, workRelationshipType } from 'fc-ui-shared-components';
import { EmploymentTabContext } from '../../../context';
import { GeneralTabContext } from '../../context';

import { WorkRelationshipContext } from './context';
import WorkRelationship from './index';

const WorkRelationshipsContainer = () => {
    const ADD_TYPE = 'work-relationship';
    const TITLE = 'Work Relationship';

    const { featureFlags } = useContext(EmploymentTabContext);
    const {
        workRelationships, editing, adding, setEditing, setAdding, workRelationshipPermissions
    } = useContext(GeneralTabContext);

    const [workRelationshipsState, setWorkRelationshipsState] = useState([]);

    useEffect(() => {
        setWorkRelationshipsState(workRelationships);
    }, [workRelationships]);

    const canCreateWorkRelationships = !featureFlags.isAnyHRMSEnabled && workRelationshipPermissions.permittedActions.View && workRelationshipPermissions.permittedActions.Create;

    const addWorkRelationshipButtonLogic = () => {
        const existing = workRelationshipsState.filter((x) => x.id !== null).map((x) => x.workRelationshipType);
        const empArr = ['Employee', 'Substitute'];
        const contingent = 'Contingent Worker';
        let disabled = !canCreateWorkRelationships || editing !== null || adding;
        let tooltip = null;
        let validNewRelationshipTypes = workRelationshipType.enums;

        if (empArr.every((x) => existing.includes(x))) {
            tooltip = 'Staff member is both an Employee and a Substitute Employee and cannot be assigned additional work relationships';
            disabled = true;
            validNewRelationshipTypes = [];
        } else if (existing.includes(contingent)) {
            tooltip = 'Staff member is a Contingent Worker and cannot be assigned additional work relationships';
            disabled = true;
            validNewRelationshipTypes = [];
        } else if (empArr.some((x) => existing.includes(x))) {
            validNewRelationshipTypes = validNewRelationshipTypes.filter((x) => !existing.includes(x.value)).filter((x) => x.value !== contingent);
        }

        return [disabled, tooltip, validNewRelationshipTypes];
    };

    const [disableAddWorkRelationshipButton, addWorkRelationshipButtonTooltip, validNewRelationshipTypes] = addWorkRelationshipButtonLogic();

    const addWorkRelationship = () => {
        setAdding(true);
        const workRelationship = {
            id: null,
            workRelationshipType: 'Employee',
            employmentGroupId: '',
            offerAcceptedDate: '',
            startDateDate: '',
            boardApprovedDate: ''
        };
        setWorkRelationshipsState([workRelationship, ...workRelationships]);
    };

    const handleAdd = () => {
        setEditing(ADD_TYPE);
        addWorkRelationship();
    };

    const cancelNewWorkRelationship = () => {
        setWorkRelationshipsState([...workRelationships.filter((r) => r.id !== null)]);
        setAdding(false);
    };

    const context = {
        validNewRelationshipTypes,
        cancelNewWorkRelationship
    };

    return (
        <WorkRelationshipContext.Provider value={context}>
            {workRelationshipPermissions.permittedActions.View
                && <AddableContainer
                    title={TITLE}
                    addType={ADD_TYPE}
                    tooltip={addWorkRelationshipButtonTooltip}
                    onAdd={canCreateWorkRelationships ? handleAdd : null}
                    disableAdd={disableAddWorkRelationshipButton || (editing !== null || adding)}>
                    {!workRelationshipsState.length ? <p className="empty">Currently no work relationships</p>
                        : workRelationshipsState.map((data) => (
                            <WorkRelationship key={data.id} workRelationship={data} />
                        ))}
                </AddableContainer>}
        </WorkRelationshipContext.Provider>
    );
};

export default WorkRelationshipsContainer;
