/* eslint-disable object-curly-newline */
import React, { useContext } from 'react';
import PropTypes from 'prop-types';
import { AddableContainer, CollapsibleSection } from 'fc-ui-shared-components';
import { EmploymentTabContext } from '../../../context';
import { GeneralTabContext } from '../../context';

import Supervisor from '.';

const SupervisorContainer = (props) => {
    const { title, addType } = props;
    const { employee } = useContext(EmploymentTabContext);
    const { supervisors, editing, adding, setEditing, addSupervisor } = useContext(GeneralTabContext);

    const { view: canViewSupervisors, edit: canEditSupervisors } = employee.$permitted.props.supervisor;

    const futureAndCurrent = supervisors.filter((s) => !s.isPast).sort((a, b) => new Date(b._from) - new Date(a._from)).map((s) => (<Supervisor key={s.id} supervisor={s} />));
    const previous = supervisors.filter((s) => s.isPast).sort((a, b) => new Date(b._from) - new Date(a._from)).map((s) => (<Supervisor key={s.id} supervisor={s} />));

    const handleAdd = () => {
        setEditing(addType);
        addSupervisor();
    };

    return (
        <>
            {canViewSupervisors
                ? <AddableContainer
                    title={title}
                    addType={addType}
                    onAdd={canEditSupervisors && handleAdd}
                    disableAdd={(editing !== null || adding)}>
                    { !supervisors.length
                        ? <p className="empty">Currently no supervisor assigned</p>
                        : <>
                            {futureAndCurrent}
                            <CollapsibleSection label="Supervisor history">
                                {previous}
                            </CollapsibleSection>
                        </>
                    }
                </AddableContainer>
                : null}
        </>
    );
};

SupervisorContainer.propTypes = {
    title: PropTypes.string,
    addType: PropTypes.string,
};

SupervisorContainer.defaultProps = {
    title: 'Supervisor',
    addType: 'supervisor',
};

export default SupervisorContainer;
