import axios from './axios';

let baseURL;
if (window.buildConfig.endpoint) {
    baseURL = window.buildConfig.endpoint.staffMember;
}

const get = async (staffId) => {
    const url = `/workrelationships/staff/${staffId}`;
    return axios.request({ baseURL, url });
};

const create = async (data) => {
    const url = '/workrelationships';
    return axios.request({
        baseURL,
        data,
        method: 'post',
        url
    });
};

const getPermissions = async (staffId) => {
    const url = `/workrelationships/permissions/${staffId}`;
    return axios.request({
        baseURL,
        method: 'get',
        url
    });
};

export default {
    create,
    get,
    getPermissions
};
