/* eslint-disable object-curly-newline */
import React, { useContext, useState } from 'react';
import {
    CollapsibleSection,
    AddableContainer,
    EffectiveDateTagConfig,
    dateHelper,
    dateByOrgHelper
} from 'fc-ui-shared-components';

import { EmploymentHistoryContext } from './context';
import { EmploymentTabContext } from '../../../context';
import { GeneralTabContext } from '../../context';
import StatusHistoryItem from './item';

const StatusHistory = () => {
    const { org, employee } = useContext(EmploymentTabContext);
    const { adding, editing, setEditing, setAdding } = useContext(GeneralTabContext);

    const { props: propPermissions } = employee.$permitted;

    const { _statusHistory: employmentHistory } = employee;

    const [newStatus, setNewStatus] = useState([]);

    function getLatestHistory() {
        return dateHelper.sortByStartDate(employmentHistory, '_from', false, true)[0];
    }

    function nextDay(serverFromDate) {
        const startDate = dateByOrgHelper.utcIsoToRelativeMoment(serverFromDate, org);
        return startDate.clone().add(1, 'days').startOf('day').toISOString();
    }

    const addEmploymentHistory = () => {
        setAdding(true);
        setEditing('status-history');
        let from = dateByOrgHelper.todaysStartRelativeTo(org, true);

        const latestHistory = getLatestHistory();
        if (latestHistory) {
            from = dateHelper.isForeverFuture(latestHistory._to) ? nextDay(latestHistory._from) : nextDay(latestHistory._to);
        }

        const newHistoryObj = {
            id: null,
            _statusId: 1,
            _from: from,
            _to: dateHelper.foreverFuture,
            _statusChangeReasonId: null,
            statusTagConfig: {}
        };

        setNewStatus([newHistoryObj]);
    };

    let futureStatuses = employmentHistory.filter((x) => dateByOrgHelper.isInFuture({
        fromDate: x._from, toDate: x._to, org
    })).map((s) => Object.assign(s, { statusTagConfig: EffectiveDateTagConfig.FUTURE }));
    futureStatuses = dateHelper.sortByStartDate(futureStatuses, '_from', false, true);

    const currentStatus = employmentHistory.find((x) => dateByOrgHelper.isCurrent({
        fromDate: x._from, toDate: x._to, org
    }));
    if (currentStatus) { currentStatus.statusTagConfig = EffectiveDateTagConfig.CURRENT; }

    let pastStatuses = employmentHistory.filter((x) => dateByOrgHelper.isInPast({
        fromDate: x._from, toDate: x._to, org
    })).map((s) => Object.assign(s, { statusTagConfig: EffectiveDateTagConfig.PAST }));
    pastStatuses = dateHelper.sortByStartDate(pastStatuses, '_from', false, true);

    const canEditStatus = propPermissions.statusHistory.edit;

    const context = {
        setNewStatus,
        employmentHistory
    };

    return (
        <div className="employment-history">
            <EmploymentHistoryContext.Provider value={context}>
                <AddableContainer
                    addType='status-history'
                    title='Status History'
                    onAdd={canEditStatus && addEmploymentHistory}
                    disableAdd={(editing !== null || adding)}>
                    {!!newStatus.length && newStatus.map((ns) => (<StatusHistoryItem key={ns.id} status={ns} />))}
                    { !employmentHistory.length && <p className="empty">No Employment History Found</p> }
                    { !!employmentHistory.length
                    && <>
                        {!!futureStatuses.length && futureStatuses.map((fs) => (<StatusHistoryItem key={fs.id} status={fs} />))}
                        {currentStatus && <StatusHistoryItem status={currentStatus} />}
                        <CollapsibleSection label='Past Statuses'>
                            {pastStatuses.map((ps) => (<StatusHistoryItem key={ps.id} status={ps} />))}
                        </CollapsibleSection>
                    </>
                    }
                </AddableContainer>
            </EmploymentHistoryContext.Provider>
        </div>
    );
};

export default StatusHistory;
