import Promise from 'core-js-pure/features/promise';

export const getEmployeeAssignments = async ($injector, employee) => {
    const ssAssignmentApi = $injector.get('ssAssignmentApi');
    const ssPositionsApi = $injector.get('ssPositionsApi');
    let positions;
    if (employee.$permitted.props.assignments.view) {
        return ssPositionsApi.getForEmployee({
            id: employee.id,
            include: 'assignments'
        }).then((data) => {
            positions = data;
            if (positions) {
                const assignmentDetailsRequests = positions.map((p) => ssAssignmentApi.getAssignment({
                    id: p.id,
                    assignmentId: p._assignments[0].id,
                    include: 'details'
                }, null, false));
                return Promise.allSettled(assignmentDetailsRequests);
            }
            return Promise.resolve([]);
        }).then((data) => {
            if (data) {
                data.forEach((assignment) => {
                    const parentPosition = positions.find((pos) => assignment.status === 'fulfilled' && pos.id === assignment.value._positionId);
                    if (parentPosition) {
                        parentPosition.assignment = assignment.value;
                    }
                });
            }
            return positions;
        });
    }
    return Promise.resolve([]);
};
