import React, {
    useEffect, useState, useContext
} from 'react';
import PropTypes from 'prop-types';
import classNames from 'classnames';
import { SelectList, SuperInput } from 'fc-ui-shared-components';
import { AssignmentContext } from '../context';
import { AssignmentDetailsContext } from './context';

const PayLaneAndStep = ({ disabled, onChange }) => {
    const {
        isEditing,
        isNew: isNewAssignment,
    } = useContext(AssignmentContext);

    const {
        formDetails,
        paySchedule,
        handleFieldUpdate,
        enablePayOverride,
        overrideStyle
    } = useContext(AssignmentDetailsContext);

    const payLanes = paySchedule.id ? paySchedule._payLanes : [];
    const [paySteps, setPaySteps] = useState([]);
    const [prevPaySchedule, setPrevPaySchedule] = useState({});

    useEffect(() => {
        if (prevPaySchedule && prevPaySchedule.id && prevPaySchedule.id !== paySchedule.id) {
            if (isEditing) {
                handleFieldUpdate('payLane', '');
                handleFieldUpdate('payStep', '');
                handleFieldUpdate('payRate', '');
            }
        }
        setPrevPaySchedule(paySchedule);

        // If jobType has payLanes and a payLane selection, set proper steps
        if (payLanes.length && formDetails.fields.payLane != null && payLanes[formDetails.fields.payLane]) {
            setPaySteps(payLanes[formDetails.fields.payLane].paySteps);
        }

        // If we don't have payLanes, clear steps array
        if (!payLanes.length) {
            setPaySteps([]);
        }
    }, [paySchedule]);

    const handlePayLaneChange = (e) => {
        const newLane = parseInt(e.target.value, 10);
        handleFieldUpdate('payLane', newLane);
        if (newLane !== formDetails.fields.payLane) {
            handleFieldUpdate('payStep', '');
            handleFieldUpdate('payRate', '');
        }
        setPaySteps(payLanes[newLane].paySteps);
        onChange(newLane, null);
    };

    const handlePayStepChange = (e) => {
        const newStep = parseInt(e.target.value, 10);
        handleFieldUpdate('payStep', newStep);
        onChange(formDetails.fields.payLane, newStep);
    };

    const shouldRequireFields = () => {
        if (isNewAssignment) {
            return Object.keys(formDetails.editModel).length !== 0;
        }
        return payLanes.length > 0;
    };

    const displayOverrideInput = enablePayOverride && (!formDetails.fields.payScheduleIsValid || formDetails.fields.usePayOverride);

    const renderPayLane = () => {
        if (displayOverrideInput) {
            return <SuperInput
                value={formDetails.fields.payOverridePayLane}
                onChange={(e) => handleFieldUpdate('payOverridePayLane', e.target.value)}
                editMode={isEditing}
                disabled={disabled}
                type="text" />;
        }
        if (!payLanes.length && !paySteps.length) {
            return 'N/A';
        }
        return <SelectList
            name={`payLane-${formDetails.id}`}
            required={shouldRequireFields()}
            value={formDetails.fields.payLane}
            editMode={isEditing}
            databindName="name"
            databindValue="laneNumber"
            items={payLanes}
            onChange={handlePayLaneChange}
            disabled={disabled} />;
    };

    const renderPayStep = () => {
        if (displayOverrideInput) {
            return <SuperInput
                value={formDetails.fields.payOverridePayStep}
                onChange={(e) => handleFieldUpdate('payOverridePayStep', e.target.value)}
                editMode={isEditing}
                disabled={disabled}
                type="text" />;
        }
        if (!payLanes.length && !paySteps.length) {
            return 'N/A';
        }
        return <SelectList
            name={`payStep-${formDetails.id}`}
            required={shouldRequireFields()}
            value={formDetails.fields.payStep}
            editMode={isEditing}
            databindName="name"
            databindValue="stepNumber"
            items={paySteps}
            onChange={handlePayStepChange}
            disabled={disabled || (!payLanes.length || formDetails.fields.payLane == null)} />;
    };

    const css = classNames({ required: !displayOverrideInput && isEditing && payLanes.length > 0 });

    return (
        <React.Fragment>
            <div className={overrideStyle('payOverridePayLane')}>
                <dt className={css}>Pay Lane</dt>
                <dd data-test="assignment-details-pay-lane">
                    {renderPayLane()}
                </dd>
            </div>
            <div className={overrideStyle('payOverridePayStep')}>
                <dt className={css}>Pay Step</dt>
                <dd data-test="assignment-details-pay-step">
                    {renderPayStep()}
                </dd>
            </div>
        </React.Fragment>
    );
};

PayLaneAndStep.propTypes = {
    disabled: PropTypes.bool,
    onChange: PropTypes.func
};

PayLaneAndStep.defaultProps = {
    disabled: false,
    onChange: () => {}
};

export default PayLaneAndStep;
