import React from 'react';
import { logger } from 'fc-ui-shared-components';
import { ASSIGNMENT_TAB } from '../../constants/actionTypes';

export const AssignmentsTabContext = React.createContext();

export const assignmentTabReducer = (state, action) => {
    const { type, payload } = action;
    switch (type) {
        case ASSIGNMENT_TAB.SET_EDIT_MODE:
            return {
                ...state,
                editing: payload
            };
        case ASSIGNMENT_TAB.SET_ADD_MODE:
            return {
                ...state,
                adding: payload
            };
        default:
            logger.warn(`Action type: ${type} not found in assignmentTabReducer.`);
            return state;
    }
};
