import React, { useContext } from 'react';
import PropTypes from 'prop-types';
import moment from 'moment';
import {
    CollapsibleSection,
    AddableContainer,
    dateByOrgHelper
} from 'fc-ui-shared-components';
import Supplemental from '.';
import { AssignmentsTabContext } from '../../context';

const SupplementalContainer = (props) => {
    const { supplementals, title, addType } = props;
    const {
        state,
        organization: org,
        supplementalTypes,
        setSupplementals,
        setEditMode,
        isPermitted
    } = useContext(AssignmentsTabContext);
    const sortDateDesc = (a, b) => (moment(a._startDateUtc) < moment(b._startDateUtc) ? 1 : -1);
    const sortNameAsc = (a, b) => {
        const atype = supplementalTypes.find((t) => a._supplementalId === t.id);
        const btype = supplementalTypes.find((t) => b._supplementalId === t.id);
        if (!atype || !btype) return 0;
        return btype._name.localeCompare(atype._name);
    };
    const newSupplemental = supplementals.find((s) => s.id === null);

    const past = supplementals
        .filter((s) => s.id !== null && dateByOrgHelper.isInPast({ fromDate: s._startDateUtc, toDate: s._endDateUtc, org }))
        .sort(sortNameAsc)
        .sort(sortDateDesc);
    const current = supplementals
        .filter((s) => s.id !== null && dateByOrgHelper.isCurrent({ fromDate: s._startDateUtc, toDate: s._endDateUtc, org }))
        .sort(sortNameAsc)
        .sort(sortDateDesc);
    const future = supplementals
        .filter((s) => s.id !== null && dateByOrgHelper.isInFuture({ fromDate: s._startDateUtc, toDate: s._endDateUtc, org }))
        .sort(sortNameAsc)
        .sort(sortDateDesc);

    const renderEmpty = () => (!supplementals.length ? <p className="empty">{`No ${title}`}</p> : null);

    const handleAdd = () => {
        setSupplementals([...supplementals, {
            id: null, _totalInstallments: '', _totalPayAmount: '', _externalId: '', _contractGroupId: null, _supplementalId: null
        }]);
        setEditMode('newSupplemental');
    };

    return (
        <AddableContainer
            title={title}
            addType={addType}
            onAdd={isPermitted('employeeSupplementals', 'create') && handleAdd}
            disableAdd={state.editing != null || state.adding !== null}>
            {renderEmpty()}
            {newSupplemental && <Supplemental key={newSupplemental.id} supplemental={newSupplemental} />}
            {future.map((s) => <Supplemental key={s.id} supplemental={s} />)}
            {current.map((s) => <Supplemental key={s.id} supplemental={s} />)}
            <CollapsibleSection label='Past Supplementals'>
                {past.map((s) => <Supplemental key={s.id} supplemental={s} />)}
            </CollapsibleSection>
        </AddableContainer>
    );
};

SupplementalContainer.propTypes = {
    title: PropTypes.string,
    addType: PropTypes.string,
    supplementals: PropTypes.arrayOf(PropTypes.object).isRequired
};

SupplementalContainer.defaultProps = {
    title: 'Supplementals',
    addType: 'supplemental',
};

export default SupplementalContainer;
