import { logger } from 'fc-ui-shared-components';

/**
 * Maps a key to an item in a list and pulls out the name or ''
 * @param {string} key The key of the item we are looking for
 * @param {object[]} Array The list that we are searching through
 */
export function mapName(key, list) {
    if (key === '') { return ''; }
    const ret = list.filter((v) => v.value === key);

    if (ret.length === 0) {
        logger.log(`Value "${key}" not found in list.`);
        return '';
    }
    return ret[0].name;
}

export function getActiveItems(list, itemId, key = 'id') {
    return list.filter((l) => l._isActive || l[key] === itemId);
}

export function getByExternalId($injector, params) {
    const ssAsync = $injector.get('ssAsync');
    const ssStorage = $injector.get('ssStorage');
    const ssListApi = $injector.get('ssListApi');
    const ssActiveUserSvc = $injector.get('ssActiveUserSvc');
    const { listExternalId } = params;

    return (ssAsync(function* () {
        // resolve immediately if the data is cached
        const orgId = yield ssActiveUserSvc.getOrgId().catch((err) => logger.log(err));
        if (orgId == null) { return []; }
        const cacheKey = `${orgId}_list_${listExternalId}`;
        const cachedList = ssStorage.load(cacheKey);
        if (cachedList != null) { return cachedList; }
        // go get the list
        const listData = yield ssListApi.query({
            externalId: listExternalId,
            include: 'Items'
        });
        if (listData.length === 0) { return []; }

        const list = listData[0];
        let data = list._items.map((item) => ({
            id: item.id,
            name: item._name,
            description: item._description,
            value: `${list.id}_${item.id}`,
            _isActive: item._isActive
        }));

        // Store the lists sorted
        data = data.sort((a, b) => {
            let nameA; let
                nameB;
            if (Object.prototype.hasOwnProperty.call(a, 'name')) {
                nameA = a.name.toUpperCase(); // ignore upper and lowercase
                nameB = b.name.toUpperCase(); // ignore upper and lowercase
            } else {
                nameA = a._name.toUpperCase();
                nameB = b._name.toUpperCase();
            }

            if (nameA < nameB) {
                return -1;
            }
            if (nameA > nameB) {
                return 1;
            }

            // names must be equal
            return 0;
        });
        ssStorage.save(cacheKey, data);
        return data;
    })());
}
