import React from 'react';
import PropTypes from 'prop-types';

const SupervisorOptionItem = ({ data, onClick }) => {
    const handleClick = () => {
        const text = `${data._firstName} ${data._lastName}`;
        onClick(text, data.id);
    };

    const handleKeypress = (e) => {
        const ENTER_KEY = 13;
        if (e.charCode === ENTER_KEY) {
            handleClick();
        }
    };
    return (
        <li
            className="action-menu-item"
            tabIndex={0}
            onClick={handleClick}
            onKeyPress={handleKeypress}
            value={data.id}>
            <div>{data.name}</div>
            <div className="ss-pipe-children">
                {data.primaryEmail && <span>{data.primaryEmail}</span>}
                {data.externalId && <span>{data.externalId}</span>}
            </div>
        </li>
    );
};

SupervisorOptionItem.propTypes = {
    data: PropTypes.shape({
        id: PropTypes.string.isRequired,
        name: PropTypes.string.isRequired,
        _firstName: PropTypes.string,
        _lastName: PropTypes.string,
        primaryEmail: PropTypes.string,
        externalId: PropTypes.string
    }).isRequired,
    onClick: PropTypes.func.isRequired
};

export default SupervisorOptionItem;
