import React from 'react';
import PropTypes from 'prop-types';
import { SuperInputCheckbox, SuperButton, Tooltip } from 'fc-ui-shared-components';
import './index.styl';

const ExportProfileItem = ({
    childContentData,
    onChangeChild
}) => {
    function showCategoryData(parentData) {
        const categoryChildren = [];
        parentData[1].childData.forEach((children) => {
            Object.entries(children).forEach((child) => categoryChildren.push(<div className="child-staff-content" key={child[0]}>
                <SuperInputCheckbox label={child[1].label} value={child[1].isChecked} disabled={!child[1].hasPermission}
                    data-test={`child-${child[0]}-${child[1].isChecked}`} onChange={() => onChangeChild(parentData, child)}></SuperInputCheckbox>
                { !child[1].hasPermission && <Tooltip text="Permissions needed" position="top">
                    <i className="permission-icon fa-lock" data-test="child-permission-lock-icon"
                        aria-label="this is non permitted field" />
                </Tooltip>}
                { child[1].isSensitive && <Tooltip text="Sensitive information" position="top">
                    <SuperButton className="sensitive-field" ssIcon="warning" data-test="sensitive-field-icon"
                        aria-label="this is a sensitive field" />
                </Tooltip>}
                {child[1].count > 0 && <label className="category-document-label">({child[1].count})</label> }
            </div>));
        });
        return categoryChildren;
    }

    return (
        <div className="child-staff-container">
            {showCategoryData(childContentData)}
        </div>
    );
};

ExportProfileItem.propTypes = {
    childContentData: PropTypes.array.isRequired,
    onChangeChild: PropTypes.func
};

ExportProfileItem.defaultProps = {
    onChangeChild: () => {}
};

export default ExportProfileItem;
