import { ASSIGNMENT } from '../constants/actionTypes';

export const editField = (field, value) => ({
    type: ASSIGNMENT.EDIT_FIELD,
    payload: { field, value }
});

export const editDetailsField = (id, field, value) => ({
    type: ASSIGNMENT.EDIT_DETAILS_FIELD,
    payload: { id, field, value }
});

export const addDetails = (id) => ({
    type: ASSIGNMENT.ADD_DETAILS,
    payload: { id }
});

export const deleteDetailsField = (id, field) => ({
    type: ASSIGNMENT.DELETE_DETAILS_FIELD,
    payload: { id, field }
});

export const removeDetail = (id) => ({
    type: ASSIGNMENT.REMOVE_DETAIL,
    payload: { id }
});

export const resetDetail = (id) => ({
    type: ASSIGNMENT.RESET_DETAIL,
    payload: { id }
});

export const createDetail = (detail) => ({
    type: ASSIGNMENT.CREATE_DETAIL,
    payload: detail
});

export const cancelAssignmentEdit = () => ({
    type: ASSIGNMENT.CANCEL
});

export const processing = (isProcessing = true) => ({
    type: ASSIGNMENT.PROCESSING,
    payload: isProcessing
});

export const showLoader = (show) => ({
    type: ASSIGNMENT.SHOWLOADER,
    payload: show
});

export const success = () => ({
    type: ASSIGNMENT.SUCCESS
});

export const updateAssignment = (assignment) => ({
    type: ASSIGNMENT.UPDATE,
    payload: assignment
});

export const editJobTypeField = (jobTypeId) => ({
    type: ASSIGNMENT.EDIT_JOB_TYPE_FIELD,
    payload: { jobTypeId }
});
