import React, { useContext } from 'react';
import PropTypes from 'prop-types';
import {
    CollapsibleSection,
    dateByOrgHelper,
    dateHelper,
    AddableContainer
} from 'fc-ui-shared-components';
import { AssignmentsTabContext } from '../../context';
import Assignment from '.';

const AssignmentContainer = (props) => {
    const { positions, title, addType } = props;
    const {
        state, organization, setEditMode, setAddMode, isPermitted
    } = useContext(AssignmentsTabContext);
    const currentAssignments = positions.filter((p) => dateByOrgHelper.isCurrent({ fromDate: p._startDate, toDate: p._endDate, org: organization })).map((p) => <Assignment key={p.id} position={p} />);
    const futureAssignments = positions.filter((p) => dateByOrgHelper.isInFuture({ fromDate: p._startDate, toDate: p._endDate, org: organization })).map((p) => <Assignment key={p.id} position={p} />);
    const pastAssignments = positions.filter((p) => dateByOrgHelper.isInPast({ fromDate: p._startDate, toDate: p._endDate, org: organization })).map((p) => <Assignment key={p.id} position={p} />);

    const newPosition = {
        id: null,
        _name: '',
        assignment: {
            _externalId: '',
            _details: [{
                isNew: true,
                id: `${(Math.ceil(Math.random() * 10000000)).toString()}`,
                _fromDateUtc: dateHelper.foreverPast,
                _toDateUtc: dateHelper.foreverFuture,
                _payPeriodsPerYear: 0,
                _payLane: '',
                _payStep: '',
                _payRateFrequency: 0,
                _hoursPerDay: 0,
                _workDaysPerYear: 0,
                _fTE: 0,
                _sickDaysPerSchoolYear: 0,
                _personalDaysPerSchoolYear: 0,
                _vacationDaysPerSchoolYear: 0
            }]
        },
        _jobType: {
            id: ''
        },
        jobType: {
            employmentGroup: ''
        },
        _contractGroupId: '',
        _location: {
            id: ''
        },
        _department: {
            id: ''
        }
    };
    const handleAdd = () => {
        setEditMode(addType);
        setAddMode(addType);
    };

    const hasAssigmentWarning = () => (state.adding === 'primary' && positions.some((p) => dateHelper.isForeverFuture(p._endDate))
        ? <p className="addable-container--warning">{`The previous ${title.replace(/s$/, '')}'s end date will auto-populate on save.`}</p> : null);

    return (
        <AddableContainer
            title={title}
            addType={addType}
            onAdd={isPermitted('assignments', 'create') && handleAdd}
            disableAdd={state.editing != null || state.adding !== null}>
            {hasAssigmentWarning()}
            {state.adding === addType && <Assignment position={newPosition} />}
            {
                !positions.length
                    ? <p className="empty">{`No ${title}`}</p>
                    : <React.Fragment>
                        {futureAssignments}
                        {currentAssignments}
                        <CollapsibleSection label='Past Assignments'>
                            {pastAssignments}
                        </CollapsibleSection>
                    </React.Fragment>
            }
        </AddableContainer>
    );
};

AssignmentContainer.propTypes = {
    title: PropTypes.string,
    addType: PropTypes.string,
    positions: PropTypes.arrayOf(PropTypes.object).isRequired
};

export default AssignmentContainer;
