export const searchStaffSupervisor = async ($injector, query, excludedStaffIdList) => {
    const ssEmployeeApi = $injector.get('ssEmployeeApi');
    const ssArrayHlp = $injector.get('ssArrayHlp');

    const params = {
        'filter[fullname]': query,
        include: 'emails,statusHistory',
        limit: 50
    };

    return ssEmployeeApi.query(params)
        .then((data) => {
            let results = data.map((staff) => {
                staff.name = staff._preferredName
                    ? `${staff._firstName} "${staff._preferredName}" ${staff._lastName}`
                    : `${staff._firstName} ${staff._lastName}`;
                const resolvedEmail = ssArrayHlp.findWhere(staff._emails, { _isPrimary: true }) || staff._emails[0] || {};
                staff.primaryEmail = resolvedEmail._emailAddress;
                staff.externalId = staff._externalId;
                return staff;
            });

            if (excludedStaffIdList) {
                results = results.filter((e) => !excludedStaffIdList.includes(e.id));
            }

            return results
                .sort((a, b) => a._firstName.localeCompare(b._firstName))
                .sort((a, b) => a._lastName.localeCompare(b._lastName));
        });
};
