import React, { useContext, useEffect, useState } from 'react';
import PropTypes from 'prop-types';
import classNames from 'classnames';
import moment from 'moment';
import {
    currencyHelper,
    dateHelper,
    dateByOrgHelper,
    jobTypeHelper,
    validationHelper,
    payRateFrequency,
    DateRangeWithStatus,
    SuperButton,
    SuperInput,
    SuperSelectList,
    ToggleSwitch
} from 'fc-ui-shared-components';
import { AssignmentDetailsContext } from './context';
import { AssignmentContext } from '../context';
import PayLaneAndStep from './payLaneAndStep';
import { AssignmentsTabContext } from '../../../context';
import './index.styl';

const overrideFields = [
    'payOverridePaySchedule',
    'payOverridePayLane',
    'payOverridePayStep',
    'payOverridePayRateFrequency'];

const AssignmentDetails = ({
    assignmentDetails, jobType, disabled, updatePreviousDetails, validDates
}) => {
    const {
        organization,
        isPermitted,
        organizationPreferences
    } = useContext(AssignmentsTabContext);

    const {
        isEditing: isAssignmentEditing,
        isNew: isNewAssignment,
        form,
        permission,
        validateDetailEndDateOverlap,
        validateDetailStartDateOverlap,
        triggerDetailDatesValidation,
    } = useContext(AssignmentContext);

    const { enablePayOverride = false } = organizationPreferences?.preferences ?? {};
    const isEditing = assignmentDetails.isNew ? true : permission.props.details.edit && isAssignmentEditing;
    const [paySchedule, setPaySchedule] = useState({});

    const getEndDate = (value) => (value == null ? dateHelper.foreverFuture : value);
    const formDetails = form.details.find((d) => d.id === assignmentDetails.id) || { id: assignmentDetails.id, editModel: {}, fields: {} };
    formDetails.fields = form.setDetailsFields(assignmentDetails, formDetails, jobType);
    const showPayOverrideToggle = enablePayOverride && formDetails.fields.payScheduleIsValid;
    const showOverriddenLabel = enablePayOverride && !isEditing && formDetails.fields.usePayOverride && formDetails.fields.payScheduleIsValid;

    const updatePaySchedule = (startDate = assignmentDetails._fromDateUtc, endDate = assignmentDetails._toDateUtc) => {
        form.deleteDetailField(assignmentDetails.id, 'payRateFrequency');
        const currentPaySchedule = jobTypeHelper.getPaySchedule({
            startDate,
            endDate,
            jobType,
            org: organization
        });
        if (currentPaySchedule.id && currentPaySchedule.id !== paySchedule.id) setPaySchedule(currentPaySchedule);
    };

    // Update 'Pay Schedule' field (assignment details) when assignment "Job Type" field (assignment) changes
    useEffect(() => {
        if (jobType.id && assignmentDetails.id) {
            // FC-20638 Pay Schedule Override may change this implementation
            updatePaySchedule(formDetails.fields.startDate, formDetails.fields.endDate);
        }
    }, [jobType]);

    const handleFieldUpdate = (field, value) => form.setDetailsField(assignmentDetails.id, field, value);

    const handleStartDateUpdate = (e) => {
        handleFieldUpdate('startDate', e);
        updatePreviousDetails(e, formDetails.id);
        updatePaySchedule(e, formDetails.fields.endDate || dateHelper.foreverFuture);
        setTimeout(triggerDetailDatesValidation, 100);
    };

    const handleEndDateUpdate = (e) => {
        handleFieldUpdate('endDate', getEndDate(e));
        updatePaySchedule(formDetails.fields.startDate, e || dateHelper.foreverFuture);
        setTimeout(triggerDetailDatesValidation, 100);
    };

    const handleUsePayOverrideUpdate = (e) => {
        handleFieldUpdate('usePayOverride', e.target.checked);
        if (!e.target.checked) {
            overrideFields.forEach((x) => handleFieldUpdate(x, (x !== 'payOverridePayRateFrequency' ? '' : formDetails.fields.payRateFrequency)));
        }
    };

    const handleDelete = () => form.deleteAssignmentDetail(assignmentDetails.id);

    const handleReset = () => {
        form.resetAssignmentDetail(assignmentDetails.id);
    };

    const resetPayRateFromPaySchedule = (payLane, payStep) => {
        form.deleteDetailField(assignmentDetails.id, 'payRate');
        const payRate = form.getValidPayRate(paySchedule, assignmentDetails, { fields: {}, editModel: { payLane, payStep } });
        handleFieldUpdate('payRate', payRate);
    };

    const fieldIsOverridden = (field) => {
        const overridden = formDetails.fields[`${field}`] !== '' && (!formDetails.fields.payScheduleIsValid || formDetails.fields.usePayOverride);
        if (field === 'payOverridePayRateFrequency') {
            return overridden && formDetails.fields[`${field}`] !== formDetails.fields.payRateFrequency;
        }
        return overridden;
    };

    const overrideStyle = (field) => {
        const canBeOverridden = enablePayOverride && formDetails.fields.payScheduleIsValid && formDetails.fields.usePayOverride;
        return classNames(
            'detail',
            {
                'payschedule-override': canBeOverridden && isEditing,
                overridden: canBeOverridden && fieldIsOverridden(field)
            }
        );
    };

    const context = {
        formDetails,
        paySchedule,
        handleFieldUpdate,
        enablePayOverride,
        overrideStyle
    };

    const validateAssignmentDetailsDates = (startDate, endDate, fieldName = 'Date', required = false) => {
        const isDateOnOrAfterStartDate = startDate
            ? (val) => val == null || moment(startDate) <= moment(dateByOrgHelper.startOfDayRelativeTo(moment(val), organization)) || `${fieldName} cannot be prior to Assignment Start Date`
            : () => { };
        const isDateOnOrBeforeEndDate = !dateHelper.isForeverFuture(endDate)
            ? (val) => val == null || moment(endDate) >= moment(dateByOrgHelper.startOfDayRelativeTo(moment(val), organization)) || `${fieldName} cannot be after Assignment End Date`
            : () => { };
        return {
            ...validationHelper.isRequired(required, fieldName),
            validate: {
                isDateOnOrAfterStartDate,
                isDateOnOrBeforeEndDate
            }
        };
    };

    const startDateRequired = !isNewAssignment || Object.keys(formDetails.editModel).length > 0;
    let startDateRules = validateAssignmentDetailsDates(form.fields.startDate, form.fields.endDate, 'Start Date', startDateRequired);
    startDateRules = {
        ...startDateRules,
        validate: {
            ...startDateRules.validate,
            noOverlap: (val) => val == null || validateDetailStartDateOverlap(formDetails.id, dateByOrgHelper.startOfDayRelativeTo(moment(val), organization)) || 'Start Date overlaps with previous End Date'
        }
    };
    let endDateRules = validateAssignmentDetailsDates(form.fields.startDate, form.fields.endDate, 'End Date');
    endDateRules = {
        ...endDateRules,
        validate: {
            ...endDateRules.validate,
            isEndDateAfterStartDate: (endDate) => endDate && validationHelper.validateEndDateIsAfterStartDate(dateByOrgHelper.startOfDayRelativeTo(moment(formDetails.fields.startDate), organization), moment(dateByOrgHelper.startOfDayRelativeTo(moment(endDate), organization))),
            noOverlap: (val) => val == null || validateDetailEndDateOverlap(formDetails.id, dateByOrgHelper.endOfDayRelativeTo(moment(val), organization)) || 'End Date overlaps with next Start Date'
        }
    };

    const displayPayScheduleName = () => {
        if (enablePayOverride && (!formDetails.fields.payScheduleIsValid || formDetails.fields.usePayOverride)) {
            return (<SuperInput
                value={formDetails.fields.payOverridePaySchedule}
                onChange={(e) => handleFieldUpdate('payOverridePaySchedule', e.target.value)}
                editMode={isEditing}
                disabled={disabled}
                type="text" />);
        }
        return formDetails.fields.payScheduleName;
    };

    const displayPayRateFrequency = () => {
        if (enablePayOverride && (!formDetails.fields.payScheduleIsValid || formDetails.fields.usePayOverride)) {
            return (<SuperSelectList
                items={payRateFrequency.enums}
                value={formDetails.fields.payOverridePayRateFrequency}
                includeNoneOption={false}
                onChange={(e) => handleFieldUpdate('payOverridePayRateFrequency', Number(e.target.value))}
                databindName="name"
                databindValue="value"
                editMode={isEditing}
                disabled={disabled} />);
        }
        if (formDetails.fields.payScheduleIsValid) {
            return !disabled ? formDetails.fields.payRateFrequencyString : '';
        }
        return (<SuperSelectList
            items={payRateFrequency.enums}
            value={formDetails.fields.payRateFrequency}
            includeNoneOption={false}
            onChange={(e) => handleFieldUpdate('payRateFrequency', Number(e.target.value))}
            databindName="name"
            databindValue="value"
            editMode={isEditing}
            disabled={disabled} />);
    };

    const displayPayPerPeriod = () => (Number.isNaN(Number.parseFloat(formDetails.fields.payPerPeriod)) ? 'N/A' : currencyHelper.currency(formDetails.fields.payPerPeriod));

    const defaultJobTypeFields = { ...jobType._currentJobTypeDetail };

    return (
        <AssignmentDetailsContext.Provider value={context}>
            <section className={classNames('assignment-details-section', { disabled })}>
                <hr className="assignment-detail-divider" />
                <header className="assignment-details-header" data-test="assignment-details-header">
                    <DateRangeWithStatus
                        startDateName={`detail-start-${assignmentDetails.id}`}
                        endDateName={`detail-end-${assignmentDetails.id}`}
                        startDateLabel="Start Date"
                        endDateLabel='End Date'
                        startDateMin={form.fields.startDate ? dateByOrgHelper.utcIsoToRelativeMoment(form.fields.startDate, organization).startOf('day').format('MM/DD/YYYY') : validDates.start.min}
                        startDateMax={form.fields.endDate ? dateByOrgHelper.utcIsoToRelativeMoment(form.fields.endDate, organization).startOf('day').format('MM/DD/YYYY') : validDates.end.max}
                        startDateRules={startDateRules}
                        endDateMin={form.fields.startDate ? dateByOrgHelper.utcIsoToRelativeMoment(form.fields.startDate, organization).startOf('day').format('MM/DD/YYYY') : validDates.start.min}
                        endDateMax={form.fields.endDate ? dateByOrgHelper.utcIsoToRelativeMoment(form.fields.endDate, organization).startOf('day').format('MM/DD/YYYY') : validDates.end.max}
                        endDateRules={endDateRules}
                        startDate={formDetails.fields.startDate}
                        isEditing={isEditing}
                        endDate={formDetails.fields.endDate}
                        organization={organization}
                        handleStartDateUpdate={handleStartDateUpdate}
                        handleEndDateUpdate={handleEndDateUpdate}
                        hideTag={true}
                        disabled={disabled}
                        startDateRequired={startDateRequired}
                        endDateRequired={false} />
                    {showOverriddenLabel && <span className="override-indicator">Pay Schedule Overridden</span>}
                    {isEditing
                    && <div className="actions">
                        <SuperButton className="super-button-container" label="Reset" type="button" styleType="smp" ssIconBefore="li-rotate-left" onClick={handleReset} disabled={Object.keys(formDetails.editModel).length === 0 || disabled} />
                        {isPermitted('assignments', 'delete') && <SuperButton className="super-button-container delete" label="Delete" type="button" styleType="flat" ssIconBefore="trash-o" onClick={handleDelete} disabled={disabled} />}
                    </div>
                    }
                </header>
                {(isEditing && showPayOverrideToggle)
                    && <div className='toggle-override-container'>
                        <ToggleSwitch label="Override Pay Schedule" value={formDetails.fields.usePayOverride || false} id={`override-${assignmentDetails.id}`} onChange={handleUsePayOverrideUpdate} styleType="warn" textAlign="left" data-test="toggle-payScheduleOverride" />
                    </div>
                }
                <dl className="assignment-details-list">
                    <div className="assignment-details-column">
                        <div className={overrideStyle('payOverridePaySchedule')}>
                            <dt>Pay Schedule</dt>
                            <dd data-test="assignment-details-pay-schedule">{displayPayScheduleName()}</dd>
                        </div>
                        <PayLaneAndStep disabled={disabled} onChange={resetPayRateFromPaySchedule} />
                        <div className={overrideStyle('payOverridePayRateFrequency')}>
                            <dt>Pay Type</dt>
                            <dd data-test="assignment-details-pay-type">
                                {displayPayRateFrequency()}
                            </dd>
                        </div>
                        <div className="detail">
                            <dt>Pay Per Period</dt>
                            <dd data-test="assignment-details-pay-per-period">{displayPayPerPeriod()}</dd>
                        </div>
                        <div className="detail">
                            <dt>Periods Per Year</dt>
                            <dd data-test="assignment-details-periods-per-year">
                                <SuperInput value={formDetails.fields.payPeriodsPerYear}
                                    onChange={(e) => handleFieldUpdate('payPeriodsPerYear', e.target.value)}
                                    editMode={isEditing}
                                    disabled={disabled}
                                    type="number"
                                    placeholder={defaultJobTypeFields.payPeriodsPerYear} /></dd>
                        </div>
                        <div className="detail">
                            <dt>{formDetails.fields.payRateFrequencyString}</dt>
                            <dd data-test="assignment-details-pay-rate">
                                <SuperInput
                                    value={isEditing ? formDetails.fields.payRate : currencyHelper.currency(formDetails.fields.payRate)}
                                    placeholder={formDetails.fields.defaultPayRate}
                                    onChange={(e) => handleFieldUpdate('payRate', e.target.value)}
                                    editMode={isEditing}
                                    type="number"
                                    disabled={disabled} />
                            </dd>
                        </div>
                    </div>
                    <div className="assignment-details-column">
                        <div className="detail">
                            <dt>FTE</dt>
                            <dd data-test="assignment-details-fte">
                                <SuperInput value={formDetails.fields.fTE}
                                    onChange={(e) => handleFieldUpdate('fTE', e.target.value)}
                                    editMode={isEditing}
                                    disabled={disabled}
                                    type="number"
                                    placeholder={defaultJobTypeFields.fte} /></dd>
                        </div>
                        <div className="detail">
                            <dt>Hours Per Day</dt>
                            <dd data-test="assignment-details-hours-per-day">
                                <SuperInput value={formDetails.fields.hoursPerDay}
                                    onChange={(e) => handleFieldUpdate('hoursPerDay', e.target.value)}
                                    editMode={isEditing}
                                    disabled={disabled}
                                    type="number"
                                    placeholder={defaultJobTypeFields.hoursPerDay} /></dd>
                        </div>
                        <div className="detail">
                            <dt>Work Days Per Year</dt>
                            <dd data-test="assignment-details-work-days">
                                <SuperInput value={formDetails.fields.workDaysPerYear}
                                    onChange={(e) => handleFieldUpdate('workDaysPerYear', e.target.value)}
                                    editMode={isEditing}
                                    disabled={disabled}
                                    type="number"
                                    placeholder={defaultJobTypeFields.workDaysPerYear} /></dd>
                        </div>
                        <div className="detail">
                            <dt>Sick Days Per Year</dt>
                            <dd data-test="assignment-details-sick-days">
                                <SuperInput value={formDetails.fields.sickDaysPerSchoolYear}
                                    onChange={(e) => handleFieldUpdate('sickDaysPerSchoolYear', e.target.value)}
                                    editMode={isEditing}
                                    disabled={disabled}
                                    type="number"
                                    placeholder={defaultJobTypeFields.sickDaysPerSchoolYear} /></dd>
                        </div>
                        <div className="detail">
                            <dt>Personal Days Per Year</dt>
                            <dd data-test="assignment-details-personal-days">
                                <SuperInput value={formDetails.fields.personalDaysPerSchoolYear}
                                    onChange={(e) => handleFieldUpdate('personalDaysPerSchoolYear', e.target.value)}
                                    editMode={isEditing}
                                    disabled={disabled}
                                    type="number"
                                    placeholder={defaultJobTypeFields.personalDaysPerSchoolYear} /></dd>
                        </div>
                        <div className="detail">
                            <dt>Vacation Days Per Year</dt>
                            <dd data-test="assignment-details-vacation-days">
                                <SuperInput value={formDetails.fields.vacationDaysPerSchoolYear}
                                    onChange={(e) => handleFieldUpdate('vacationDaysPerSchoolYear', e.target.value)}
                                    editMode={isEditing}
                                    disabled={disabled}
                                    type="number"
                                    placeholder={defaultJobTypeFields.vacationDaysPerSchoolYear} /></dd>
                        </div>
                    </div>
                </dl>
            </section>
        </AssignmentDetailsContext.Provider>
    );
};

AssignmentDetails.propTypes = {
    assignmentDetails: PropTypes.object.isRequired,
    validDates: PropTypes.exact({
        start: PropTypes.exact({
            min: PropTypes.string,
            max: PropTypes.string
        }),
        end: PropTypes.exact({
            min: PropTypes.string,
            max: PropTypes.string
        })
    }).isRequired,
    jobType: PropTypes.object.isRequired,
    updatePreviousDetails: PropTypes.func.isRequired,
    disabled: PropTypes.bool
};

AssignmentDetails.defaultProps = {
    disabled: false
};

export default AssignmentDetails;
