import axios from 'axios';
import { idmTokenHelper, idmDataHelper } from 'fc-ui-shared-components';

axios.interceptors.request.use(async (config) => {
    if (!config.headers.authorization) {
        const token = idmTokenHelper.getAccessToken();
        if (token) {
            config.headers.authorization = `Bearer ${token}`;
        }
    }
    if (!config.headers.flUser) {
        const userContext = await idmDataHelper.getUserContext();
        const currentEmployee = await idmDataHelper.getCurrentEmployee();
        config.headers.flUser = JSON.stringify({ ...userContext, UserKey: currentEmployee });
    }
    return config;
}, (err) => Promise.reject(err));

export default axios;
